import { CommonModule } from '@angular/common';
import { Component, input, output, signal, computed } from '@angular/core';
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormsModule,
} from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { DialogModule } from 'primeng/dialog';
import { IconField } from 'primeng/iconfield';
import { InputIcon } from 'primeng/inputicon';
import { InputTextModule } from 'primeng/inputtext';
import { ScrollPanelModule } from 'primeng/scrollpanel';

// Common Material Icons for alerts
const MATERIAL_ICONS = [
  // Alert & Warning Icons
  'warning',
  'error',
  'info',
  'check_circle',
  'cancel',
  'help',
  'priority_high',
  'notification_important',
  'report_problem',
  'security',

  // Transportation & Vehicle Icons
  'directions_car',
  'local_shipping',
  'directions_bus',
  'motorcycle',
  'local_taxi',
  'airport_shuttle',
  'fire_truck',
  'emergency',

  // Location & Navigation
  'location_on',
  'gps_fixed',
  'place',
  'map',
  'navigation',
  'my_location',
  'location_off',
  'location_searching',

  // Time & Schedule
  'schedule',
  'access_time',
  'timer',
  'alarm',
  'today',
  'event',
  'history',
  'update',

  // Communication & Notifications
  'notifications',
  'notifications_active',
  'notifications_off',
  'email',
  'message',
  'phone',
  'chat',
  'announcement',

  // Status & Monitoring
  'visibility',
  'visibility_off',
  'monitor',
  'dashboard',
  'analytics',
  'trending_up',
  'trending_down',
  'speed',

  // Actions & Controls
  'play_arrow',
  'pause',
  'stop',
  'refresh',
  'sync',
  'power_settings_new',
  'settings',
  'tune',

  // General Purpose
  'home',
  'business',
  'person',
  'group',
  'folder',
  'description',
  'assignment',
  'bookmark',

  // Technical & System
  'memory',
  'storage',
  'wifi',
  'signal_cellular_alt',
  'battery_alert',
  'battery_full',
  'network_check',
  'router',
];

@Component({
  selector: 'app-icon-selector',
  imports: [
    CommonModule,
    FormsModule,
    ButtonModule,
    DialogModule,
    InputTextModule,
    ScrollPanelModule,
    IconField,
    InputIcon,
  ],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: IconSelectorComponent,
      multi: true,
    },
  ],
  templateUrl: './icon-selector.component.html',
  styleUrl: './icon-selector.component.scss',
})
export class IconSelectorComponent implements ControlValueAccessor {
  // Input properties
  readonly placeholder = input<string>('Select an icon');
  readonly disabled = input<boolean>(false);

  // Output events
  readonly iconChange = output<string | null>();

  // Internal state
  readonly dialogVisible = signal<boolean>(false);
  readonly searchTerm = signal<string>('');
  readonly value = signal<string | null>(null);

  // Available icons
  readonly availableIcons = signal<string[]>(MATERIAL_ICONS);

  // Filtered icons based on search
  readonly filteredIcons = computed(() => {
    const search = this.searchTerm().toLowerCase().trim();
    if (!search) {
      return this.availableIcons();
    }
    return this.availableIcons().filter((icon) =>
      icon.toLowerCase().includes(search),
    );
  });

  // ControlValueAccessor implementation
  private onChange = (_value: string | null) => {};
  private onTouched = () => {};

  showDialog(): void {
    if (!this.disabled()) {
      this.dialogVisible.set(true);
      this.searchTerm.set('');
    }
  }

  selectIcon(icon: string): void {
    this.value.set(icon);
    this.onChange(icon);
    this.onTouched();
    this.iconChange.emit(icon);
    this.dialogVisible.set(false);
  }

  clearSelection(): void {
    this.value.set(null);
    this.onChange(null);
    this.onTouched();
    this.iconChange.emit(null);
  }

  // ControlValueAccessor methods
  writeValue(value: string | null): void {
    this.value.set(value);
  }

  registerOnChange(fn: (value: string | null) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(_isDisabled: boolean): void {
    // Handle disabled state if needed
  }
}
