import { inject, Injectable, signal } from '@angular/core';
import { isDefined, isNullOrUndefined } from '../utils';
import { ConditionPart, FilterCondition, TreeSelectNode } from '../models';
import { EventFilterService } from './event-filter.service';
import { isEmpty } from 'lodash';

@Injectable({
  providedIn: 'root',
})
export class EventFilterConditionService {
  protected eventFilterService = inject(EventFilterService);
  private readonly conditionPartsCache = new Map<string, ConditionPart[]>();
  zoneOptions = signal<TreeSelectNode[]>([]);
  digitalInputOptions = signal<{ label: string; value: string }[]>([]);
  digitalStateOptions = signal<{ label: string; value: string }[]>([]);
  stateProfileOptions = signal<{ label: string; value: string }[]>([]);
  stateValueOptions = signal<{ label: string; value: string }[]>([]);
  linkedAssetOptions = signal<TreeSelectNode[]>([]);

  async initializeConditionParts(condition: FilterCondition): Promise<void> {
    if (condition.type === 'zone') {
      const currentZoneOptions = this.zoneOptions();
      // Check if zoneOptions need to be fetched
      if (isEmpty(currentZoneOptions) || currentZoneOptions.length === 0) {
        this.eventFilterService
          .getZoneSelectionTree()
          .then((nodes) => {
            this.zoneOptions.set(nodes);
            // Ensure the cache for the current condition is updated with the new options
            this.updateOptionsInCache(condition, 'zone', 'zone');
          })
          .catch(() => {
            // Handle error, potentially set zoneOptions to an empty array or a default error state
            this.zoneOptions.set([]);
          });
      } else {
        // zoneOptions are already loaded, just ensure this condition's cache is up-to-date
        this.updateOptionsInCache(condition, 'zone', 'zone');
      }
    }

    // Add handling for digital input conditions
    if (condition.type === 'digital') {
      await this.loadDigitalInputOptions(condition);
      await this.loadDigitalStateOptions(condition);
    }

    // Add handling for state conditions
    if (condition.type === 'state') {
      await this.loadStateProfileOptions(condition);
    }

    // Add handling for linked asset conditions
    if (condition.type === 'linked') {
      await this.loadLinkedAssetOptions(condition);
    }
  }

  private updateOptionsInCache(
    condition: FilterCondition,
    key: string,
    conditionType: string,
  ) {
    const cacheKey = `${condition.type}-${condition.id}`;
    const cachedParts = this.conditionPartsCache.get(cacheKey);

    if (!cachedParts) return;

    const part = cachedParts.find((p) => p.key === key);
    if (!isDefined(part)) return;

    // Get the options from the appropriate signal
    const optionsKey = `${conditionType}-${key}`;

    switch (optionsKey) {
      case 'zone-zone':
        part.options = this.zoneOptions();
        break;
      case 'digital-input':
        part.options = this.digitalInputOptions();
        break;
      case 'digital-state':
        part.options = this.digitalStateOptions();
        break;
      case 'state-profile':
        part.options = this.stateProfileOptions();
        break;
      case 'state-state':
        part.options = this.stateValueOptions();
        break;
      case 'linked-asset':
        part.options = this.linkedAssetOptions();
        break;
    }
  }

  private async loadDigitalInputOptions(
    condition: FilterCondition,
  ): Promise<void> {
    try {
      const conditionDef = this.eventFilterService.EVENT_CONDITIONS['digital'];
      const inputFieldDef = conditionDef['input'];

      // Get the input options by calling the function
      if (typeof inputFieldDef.values === 'function') {
        const inputOptions = await inputFieldDef.values();

        // Transform the options to match ConditionPartOption format
        const transformedOptions = (
          inputOptions as { id: string; label: string }[]
        ).map((opt) => ({
          label: opt.label,
          value: opt.id,
        }));

        // Store in signal
        this.digitalInputOptions.set(transformedOptions);

        // Update cache with options from signal
        this.updateOptionsInCache(condition, 'input', 'digital');

        // If an input is already selected, load its state options
        if (isDefined(condition.values['input'])) {
          await this.loadDigitalStateOptions(condition);
        }
      }
    } catch (error) {
      console.error('Error loading digital input options:', error);
      this.digitalInputOptions.set([]);
    }
  }

  private async loadDigitalStateOptions(
    condition: FilterCondition,
  ): Promise<void> {
    try {
      const inputId = condition.values['input'] as string;

      if (!isDefined(inputId) || inputId === '') {
        // No input selected, clear state options
        this.digitalStateOptions.set([]);
        return;
      }

      const conditionDef = this.eventFilterService.EVENT_CONDITIONS['digital'];
      const stateFieldDef = conditionDef['state'];

      // Get the state options by calling the function with the input parameter
      if (typeof stateFieldDef.values === 'function') {
        const stateOptions = await stateFieldDef.values({ input: inputId });

        // Transform the options to match ConditionPartOption format
        const transformedOptions = (
          stateOptions as { id: string; label: string }[]
        ).map((opt) => ({
          label: opt.label,
          value: opt.id,
        }));

        // Store in signal
        this.digitalStateOptions.set(transformedOptions);

        // Update cache with options from signal
        this.updateOptionsInCache(condition, 'state', 'digital');
      }
    } catch (error) {
      console.error('Error loading digital state options:', error);
      this.digitalStateOptions.set([]);
    }
  }

  private async loadStateProfileOptions(
    condition: FilterCondition,
  ): Promise<void> {
    try {
      const conditionDef = this.eventFilterService.EVENT_CONDITIONS['state'];
      const profileFieldDef = conditionDef['profile'];

      // Get the profile options
      if (typeof profileFieldDef.values === 'function') {
        const profileOptions = await profileFieldDef.values();

        // Transform the options to match ConditionPartOption format
        const transformedOptions = (
          profileOptions as { id: string; label: string }[]
        ).map((opt) => ({
          label: opt.label,
          value: opt.id,
        }));

        // Store in signal
        this.stateProfileOptions.set(transformedOptions);

        // Update cache with options from signal
        this.updateOptionsInCache(condition, 'profile', 'state');

        // If a profile is selected, load the state options
        if (isDefined(condition.values['profile'])) {
          await this.loadStateValues(condition);
        }
      }
    } catch (error) {
      console.error('Error loading state profile options:', error);
      this.stateProfileOptions.set([]);
    }
  }

  private async loadStateValues(condition: FilterCondition): Promise<void> {
    try {
      const conditionDef = this.eventFilterService.EVENT_CONDITIONS['state'];
      const stateFieldDef = conditionDef['state'];

      // Get the state options by calling the function with current profile value
      if (typeof stateFieldDef.values === 'function') {
        const stateOptions = await stateFieldDef.values({
          profile: condition.values['profile'] as string,
        });

        // Transform the options to match ConditionPartOption format
        const transformedOptions = (
          stateOptions as { id: string; label: string }[]
        ).map((opt) => ({
          label: opt.label,
          value: opt.id,
        }));

        // Store in signal
        this.stateValueOptions.set(transformedOptions);

        // Update cache with options from signal
        this.updateOptionsInCache(condition, 'state', 'state');
      }
    } catch (error) {
      console.error('Error loading state values:', error);
      this.stateValueOptions.set([]);
    }
  }

  private async loadLinkedAssetOptions(
    condition: FilterCondition,
  ): Promise<void> {
    try {
      const conditionDef = this.eventFilterService.EVENT_CONDITIONS['linked'];
      const assetFieldDef = conditionDef['asset'];

      // Get the asset options by calling the function
      if (typeof assetFieldDef.values === 'function') {
        const assetOptions = await assetFieldDef.values();

        // Store in signal (already in TreeSelectNode format, no transformation needed)
        this.linkedAssetOptions.set(assetOptions as TreeSelectNode[]);

        // Update cache with options from signal
        this.updateOptionsInCache(condition, 'asset', 'linked');
      }
    } catch (error) {
      console.error('Error loading linked asset options:', error);
      this.linkedAssetOptions.set([]);
    }
  }

  // Public method to handle when a field value changes (for linked fields)
  async onConditionFieldChange(
    condition: FilterCondition,
    fieldKey: string,
  ): Promise<ConditionPart[]> {
    if (condition.type === 'state' && fieldKey === 'profile') {
      await this.loadStateValues(condition);
    } else if (condition.type === 'digital' && fieldKey === 'input') {
      await this.loadDigitalStateOptions(condition);
    }
    return this.getConditionParts(condition);
  }

  private updateSpeedConditionText(condition: FilterCondition): void {
    const value = (condition.values['value'] as string) ?? '0';
    const unit = (condition.values['unit'] as string) ?? 'mph';
    const operator = (condition.values['operator'] as string) ?? 'gt';

    let operatorText = 'equal to';
    if (operator === 'gt') {
      operatorText = 'greater than';
    } else if (operator === 'lt') {
      operatorText = 'less than';
    }

    condition.text = {
      ...condition.text,
      display: `speed is ${operatorText} ${value} ${unit}`,
      value: `${value}`,
      unit,
      operator: operatorText,
      speed: `${value} ${unit}`,
    };
  }

  private updateTimeConditionText(condition: FilterCondition): void {
    const mode = (condition.values['mode'] as string) ?? 'inside';
    const time1 = (condition.values['time1'] as string) ?? '00:00';
    const time2 = (condition.values['time2'] as string) ?? '23:59';
    const dow = (condition.values['dow'] as string[]) ?? [];

    const modeText = mode === 'inside' ? 'between' : 'outside of';
    let dowText = '';

    if (isDefined(dow) && dow.length > 0) {
      const dayNames = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
      const selectedDays = dow.map((d: string) => dayNames[Number.parseInt(d)]);
      dowText = ` on ${selectedDays.join(', ')}`;
    }

    condition.text = {
      ...condition.text,
      display: `time is ${modeText} ${time1} and ${time2}${dowText}`,
      mode: modeText,
      time1,
      time2,
      dow,
    };
  }

  private updateZoneConditionText(condition: FilterCondition): void {
    const mode = condition.values['mode'] ?? 'inside';
    const zoneName = (condition.values['zoneName'] as string) ?? 'unknown zone';
    const zoneId = (condition.values['zoneId'] as string) ?? '';

    const modeText = mode === 'inside' ? 'inside' : 'outside';

    condition.text = {
      ...condition.text,
      display: `current position is ${modeText} of ${zoneName}`,
      mode: modeText,
      zoneName,
      zoneId,
    };
  }

  private updateLinkedConditionText(condition: FilterCondition): void {
    const mode = condition.values['mode'] ?? 'in';
    const assetName =
      (condition.values['assetName'] as string) ??
      (condition.values['asset'] as unknown as TreeSelectNode)?.label ??
      'unknown asset';
    const assetId = (condition.values['assetId'] as string) ?? '';

    const modeText = mode === 'in' ? 'is' : 'is not';

    condition.text = {
      ...condition.text,
      display: `current linked asset ${modeText} ${assetName}`,
      mode: modeText,
      assetName,
      assetId,
    };
  }

  private updateActiveConditionText(condition: FilterCondition): void {
    const state = condition.values['state'] ?? '1';
    const stateText = state === '1' ? 'On' : 'Off';

    condition.text = {
      ...condition.text,
      display: `ignition is ${stateText}`,
      state: stateText,
    };
  }

  private updateDigitalConditionText(condition: FilterCondition): void {
    const input = (condition.values['input'] as string) ?? '';
    const inputName =
      (condition.values['inputName'] as string) ??
      this.digitalInputOptions().find((opt) => opt.value === input)?.label ??
      'unknown input';
    const state = (condition.values['state'] as string) ?? '1';
    const stateName =
      (condition.values['stateName'] as string) ??
      this.digitalStateOptions().find((opt) => opt.value === state)?.label ??
      (state === '1' ? 'active' : 'inactive');

    condition.text = {
      ...condition.text,
      display: `digital ${inputName} is ${stateName}`,
      input,
      inputName,
      state,
      stateName,
    };
  }

  private updateStateConditionText(condition: FilterCondition): void {
    const profile = (condition.values['profile'] as string) ?? '';
    const profileName =
      (condition.values['profileName'] as string) ??
      this.stateProfileOptions().find((opt) => opt.value === profile)?.label ??
      'unknown profile';
    const state = (condition.values['state'] as string) ?? '';
    const stateName = (condition.values['stateName'] as string) ?? 'state';

    condition.text = {
      ...condition.text,
      display: `state ${profileName} is ${stateName}`,
      profile,
      profileName,
      state,
      stateName,
    };
  }

  private updateDistanceConditionText(condition: FilterCondition): void {
    const mode = condition.values['mode'] ?? 'greater';
    const distance = condition.values['distance'] ?? '0';
    const unit = (condition.values['unit'] as string) ?? 'mi';

    const modeText = mode === 'greater' ? 'greater than' : 'less than';

    condition.text = {
      ...condition.text,
      display: `distance is ${modeText} ${distance} ${unit}`,
      mode: modeText,
      distance: `${distance}`,
      unit,
    };
  }

  private updateDurationConditionText(condition: FilterCondition): void {
    const mode = condition.values['mode'] ?? 'greater';
    const duration = condition.values['duration'] ?? '0';
    const unit = (condition.values['unit'] as string) ?? 'minutes';

    const modeText = mode === 'greater' ? 'greater than' : 'less than';

    condition.text = {
      ...condition.text,
      display: `duration is ${modeText} ${duration} ${unit}`,
      mode: modeText,
      duration: `${duration}`,
      unit,
    };
  }

  private updateRouteNumberConditionText(condition: FilterCondition): void {
    const mode = (condition.values['mode'] as string) ?? 'equals';
    const value = (condition.values['value'] as string) ?? '';
    const modeText = this.getComparisonModeText(mode);

    condition.text = {
      ...condition.text,
      display: `route number ${modeText} ${value}`,
      mode: modeText,
      value,
    };
  }

  private updateRoadNameConditionText(condition: FilterCondition): void {
    const mode = (condition.values['mode'] as string) ?? 'equals';
    const value = (condition.values['value'] as string) ?? '';
    const modeText = this.getComparisonModeText(mode);

    condition.text = {
      ...condition.text,
      display: `road name ${modeText} ${value}`,
      mode: modeText,
      value,
    };
  }

  private updateBoundaryConditionText(condition: FilterCondition): void {
    const boundary = (condition.values['boundary'] as string) ?? 'suburb';
    const mode = (condition.values['mode'] as string) ?? 'equals';
    const value = (condition.values['value'] as string) ?? '';

    const boundaryText = this.getBoundaryText(boundary);
    const modeText = this.getComparisonModeText(mode);

    condition.text = {
      ...condition.text,
      display: `${boundaryText} ${modeText} ${value}`,
      boundary: boundaryText,
      mode: modeText,
      value,
    };
  }

  private updateSpeedLimitConditionText(condition: FilterCondition): void {
    const mode = (condition.values['mode'] as string) ?? 'greater';
    const speed = (condition.values['speed'] as string) ?? '0';
    const unit = (condition.values['unit'] as string) ?? 'mph';

    let modeText;
    switch (mode) {
      case 'greater':
        modeText = 'is greater than';
        break;
      case 'less':
        modeText = 'is less than';
        break;
      case 'equals':
        modeText = 'equals';
        break;
      default:
        modeText = 'is greater than';
    }

    condition.text = {
      ...condition.text,
      display: `speed limit ${modeText} ${speed} ${unit}`,
      mode: modeText,
      speed: `${speed}`,
      unit,
    };
  }

  private updateCarriagewayConditionText(condition: FilterCondition): void {
    const mode = condition.values['mode'] ?? 'single';
    const modeText = mode === 'single' ? 'single' : 'dual';

    condition.text = {
      ...condition.text,
      display: `road is a ${modeText} carriageway`,
      mode: modeText,
    };
  }

  private updateDaysConditionText(condition: FilterCondition): void {
    const days = (condition.values['days'] as string[]) ?? [];
    condition.text = {
      ...condition.text,
      display: days.length > 0 ? `on days: ${days.join(', ')}` : 'on all days',
      days,
    };
  }

  private updateDefaultConditionText(condition: FilterCondition): void {
    const parts = this.getConditionParts(condition);
    const textParts: string[] = [];

    for (const part of parts) {
      if (isDefined(condition.values[part.key])) {
        if (part.type === 'dropdown') {
          const option = part.options?.find(
            (opt) => opt.value === condition.values[part.key],
          );
          if (isDefined(option)) {
            textParts.push(`${part.label}: ${option.label}`);
          }
        } else if (
          part.type === 'checklist' &&
          Array.isArray(condition.values[part.key])
        ) {
          if ((condition.values[part.key] as string[]).length > 0) {
            textParts.push(
              `${part.label}: ${(condition.values[part.key] as string[]).join(', ')}`,
            );
          }
        } else {
          textParts.push(`${part.label}: ${condition.values[part.key]}`);
        }
      }
    }

    condition.text = {
      ...condition.text,
      display: textParts.join(', '),
    };
  }

  // Helper functions to further reduce complexity
  private getComparisonModeText(mode: string): string {
    switch (mode) {
      case 'equals':
        return 'equals';
      case 'contains':
        return 'contains';
      case 'starts':
        return 'starts with';
      case 'ends':
        return 'ends with';
      case 'not_equals':
        return 'does not equal';
      case 'not_contains':
        return 'does not contain';
      case 'not_starts':
        return 'does not start with';
      case 'not_ends':
        return 'does not end with';
      default:
        return 'equals';
    }
  }

  private getBoundaryText(boundary: string): string {
    switch (boundary) {
      case 'suburb':
        return 'suburb';
      case 'town':
        return 'town';
      case 'state':
        return 'state/province';
      case 'country':
        return 'country';
      default:
        return 'suburb';
    }
  }

  updateConditionText(condition: FilterCondition): void {
    switch (condition.type) {
      case 'speed':
        this.updateSpeedConditionText(condition);
        break;
      case 'time':
        this.updateTimeConditionText(condition);
        break;
      case 'zone':
        this.updateZoneConditionText(condition);
        break;
      case 'linked':
        this.updateLinkedConditionText(condition);
        break;
      case 'active':
        this.updateActiveConditionText(condition);
        break;
      case 'digital':
        this.updateDigitalConditionText(condition);
        break;
      case 'state':
        this.updateStateConditionText(condition);
        break;
      case 'distance':
        this.updateDistanceConditionText(condition);
        break;
      case 'duration':
        this.updateDurationConditionText(condition);
        break;
      case 'routenumber':
        this.updateRouteNumberConditionText(condition);
        break;
      case 'roadname':
        this.updateRoadNameConditionText(condition);
        break;
      case 'boundary':
        this.updateBoundaryConditionText(condition);
        break;
      case 'speedlimit':
        this.updateSpeedLimitConditionText(condition);
        break;
      case 'carriageway':
        this.updateCarriagewayConditionText(condition);
        break;
      case 'days':
        this.updateDaysConditionText(condition);
        break;
      default:
        this.updateDefaultConditionText(condition);
    }
  }

  getConditionParts(condition: FilterCondition | null): ConditionPart[] {
    if (isNullOrUndefined(condition)) return [];

    const cacheKey = `${condition.type}-${condition.id}`;

    if (this.conditionPartsCache.has(cacheKey)) {
      return (this.conditionPartsCache.get(cacheKey) as ConditionPart[]) ?? [];
    }

    const parts: ConditionPart[] = [];

    switch (condition.type) {
      case 'speed':
        parts.push(
          {
            key: 'value',
            label: 'Speed Value',
            type: 'number',
            value: condition.values['value'] ?? 0,
            required: true,
          },
          {
            key: 'operator',
            label: 'Operator',
            type: 'dropdown',
            options: [
              { label: 'Greater than', value: 'gt' },
              { label: 'Less than', value: 'lt' },
              { label: 'Equal to', value: 'eq' },
            ],
            value: condition.values['operator'] ?? 'gt',
            required: true,
          },
          {
            key: 'unit',
            label: 'Unit',
            type: 'dropdown',
            options: [
              { label: 'MPH', value: 'mph' },
              { label: 'KPH', value: 'kph' },
            ],
            value: condition.values['unit'] ?? 'mph',
            required: true,
          },
        );
        break;

      case 'time':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Between', value: 'inside' },
              { label: 'Outside of', value: 'outside' },
            ],
            value: condition.values['mode'] ?? 'inside',
            required: true,
          },
          {
            key: 'time1',
            label: 'Start Time',
            type: 'time',
            value: condition.values['time1'] ?? '00:00',
            required: true,
          },
          {
            key: 'time2',
            label: 'End Time',
            type: 'time',
            value: condition.values['time2'] ?? '23:59',
            required: true,
          },
          {
            key: 'dow',
            label: 'Days of Week',
            type: 'checklist',
            options: [
              { label: 'Sun', value: '0' },
              { label: 'Mon', value: '1' },
              { label: 'Tue', value: '2' },
              { label: 'Wed', value: '3' },
              { label: 'Thu', value: '4' },
              { label: 'Fri', value: '5' },
              { label: 'Sat', value: '6' },
            ],
            value: condition.values['dow'] ?? [],
            required: false,
          },
        );
        break;

      case 'zone':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Inside', value: 'inside' },
              { label: 'Outside', value: 'outside' },
            ],
            value: condition.values['mode'] ?? 'inside',
            required: true,
          },
          {
            key: 'zone',
            label: 'Geofence',
            type: 'searchableTree',
            options: this.zoneOptions(),
            value: condition.values['zone'] ?? '',
            required: true,
          },
        );
        this.conditionPartsCache.set(cacheKey, parts);

        break;

      case 'linked':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Is', value: 'in' },
              { label: 'Is Not', value: 'out' },
            ],
            value: condition.values['mode'] ?? 'in',
            required: true,
          },
          {
            key: 'asset',
            label: 'Asset',
            type: 'searchableTree',
            options: this.linkedAssetOptions(),
            value: condition.values['asset'] ?? '',
            required: true,
          },
        );
        this.conditionPartsCache.set(cacheKey, parts);
        break;

      case 'active':
        parts.push({
          key: 'state',
          label: 'State',
          type: 'dropdown',
          options: [
            { label: 'On', value: '1' },
            { label: 'Off', value: '0' },
          ],
          value: condition.values['state'] ?? '1',
          required: true,
        });
        break;

      case 'digital':
        parts.push(
          {
            key: 'input',
            label: 'Input',
            type: 'dropdown',
            options: this.digitalInputOptions(),
            value: condition.values['input'] ?? '',
            required: true,
          },
          {
            key: 'state',
            label: 'State',
            type: 'dropdown',
            options: this.digitalStateOptions(),
            value: condition.values['state'] ?? '1',
            required: true,
          },
        );
        this.conditionPartsCache.set(cacheKey, parts);
        break;

      case 'state':
        parts.push(
          {
            key: 'profile',
            label: 'Profile',
            type: 'dropdown',
            options: this.stateProfileOptions(),
            value: condition.values['profile'] ?? '',
            required: true,
          },
          {
            key: 'state',
            label: 'State',
            type: 'dropdown',
            options: this.stateValueOptions(),
            value: condition.values['state'] ?? '',
            required: true,
          },
        );
        this.conditionPartsCache.set(cacheKey, parts);
        break;

      case 'distance':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Greater than', value: 'greater' },
              { label: 'Less than', value: 'less' },
            ],
            value: condition.values['mode'] ?? 'greater',
            required: true,
          },
          {
            key: 'distance',
            label: 'Distance',
            type: 'number',
            value: condition.values['distance'] ?? 0,
            required: true,
          },
          {
            key: 'unit',
            label: 'Unit',
            type: 'dropdown',
            options: [
              { label: 'Miles', value: 'mi' },
              { label: 'Kilometers', value: 'km' },
              { label: 'Meters', value: 'm' },
            ],
            value: condition.values['unit'] ?? 'mi',
            required: true,
          },
        );
        break;

      case 'duration':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Greater than', value: 'greater' },
              { label: 'Less than', value: 'less' },
            ],
            value: condition.values['mode'] ?? 'greater',
            required: true,
          },
          {
            key: 'duration',
            label: 'Duration',
            type: 'number',
            value: condition.values['duration'] ?? 0,
            required: true,
          },
          {
            key: 'unit',
            label: 'Unit',
            type: 'dropdown',
            options: [
              { label: 'Seconds', value: 'seconds' },
              { label: 'Minutes', value: 'minutes' },
              { label: 'Hours', value: 'hours' },
            ],
            value: condition.values['unit'] ?? 'minutes',
            required: true,
          },
        );
        break;

      case 'routenumber':
      case 'roadname':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Equals', value: 'equals' },
              { label: 'Contains', value: 'contains' },
              { label: 'Starts with', value: 'starts' },
              { label: 'Ends with', value: 'ends' },
              { label: 'Does not equal', value: 'not_equals' },
              { label: 'Does not contain', value: 'not_contains' },
              { label: 'Does not start with', value: 'not_starts' },
              { label: 'Does not end with', value: 'not_ends' },
            ],
            value: condition.values['mode'] ?? 'equals',
            required: true,
          },
          {
            key: 'value',
            label:
              condition.type === 'routenumber' ? 'Route Number' : 'Road Name',
            type: 'text',
            value: condition.values['value'] ?? '',
            required: true,
          },
        );
        break;

      case 'boundary':
        parts.push(
          {
            key: 'boundary',
            label: 'Boundary Type',
            type: 'dropdown',
            options: [
              { label: 'Suburb', value: 'suburb' },
              { label: 'Town', value: 'town' },
              { label: 'State/Province', value: 'state' },
              { label: 'Country', value: 'country' },
            ],
            value: condition.values['boundary'] ?? 'suburb',
            required: true,
          },
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Equals', value: 'equals' },
              { label: 'Contains', value: 'contains' },
              { label: 'Starts with', value: 'starts' },
              { label: 'Ends with', value: 'ends' },
              { label: 'Does not equal', value: 'not_equals' },
              { label: 'Does not contain', value: 'not_contains' },
              { label: 'Does not start with', value: 'not_starts' },
              { label: 'Does not end with', value: 'not_ends' },
            ],
            value: condition.values['mode'] ?? 'equals',
            required: true,
          },
          {
            key: 'value',
            label: 'Value',
            type: 'text',
            value: condition.values['value'] ?? '',
            required: true,
          },
        );
        break;

      case 'speedlimit':
        parts.push(
          {
            key: 'mode',
            label: 'Mode',
            type: 'dropdown',
            options: [
              { label: 'Is greater than', value: 'greater' },
              { label: 'Is less than', value: 'less' },
              { label: 'Equals', value: 'equals' },
            ],
            value: condition.values['mode'] ?? 'greater',
            required: true,
          },
          {
            key: 'speed',
            label: 'Speed',
            type: 'number',
            value: condition.values['speed'] ?? 0,
            required: true,
          },
          {
            key: 'unit',
            label: 'Unit',
            type: 'dropdown',
            options: [
              { label: 'MPH', value: 'mph' },
              { label: 'KPH', value: 'kph' },
            ],
            value: condition.values['unit'] ?? 'mph',
            required: true,
          },
        );
        break;

      case 'carriageway':
        parts.push({
          key: 'mode',
          label: 'Mode',
          type: 'dropdown',
          options: [
            { label: 'Single', value: 'single' },
            { label: 'Dual', value: 'dual' },
          ],
          value: condition.values['mode'] ?? 'single',
          required: true,
        });
        break;

      default:
        // For any other condition type, just add generic text editing for all values
        if (isDefined(condition.values)) {
          for (const key of Object.keys(condition.values)) {
            if (key === 'id' || key === 'type') continue; // Skip technical fields

            const valueType = typeof condition.values[key];
            parts.push({
              key,
              label: key.charAt(0).toUpperCase() + key.slice(1),
              type: valueType === 'number' ? 'number' : 'text',
              value: condition.values[key] ?? '',
              required: true,
            });
          }
        }
    }

    return parts;
  }
}
