import {
  Component,
  ChangeDetectionStrategy,
  input,
  model,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';

import { OverlayModule } from 'primeng/overlay';
import { SelectModule } from 'primeng/select';

import { TreeNode } from 'primeng/api';
import { TreeSelectNode, TargetNodeData } from '../../../../models';

@Component({
  selector: 'app-target-selector',
  standalone: true,
  imports: [CommonModule, FormsModule, OverlayModule, SelectModule],
  templateUrl: './target-selector.component.html',
  styleUrls: ['./target-selector.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TargetSelectorComponent {
  readonly targetType = input<string | undefined>(undefined);
  readonly targets = input<TreeNode[]>([]);

  showTargetDropdown = false;
  target = model<TreeSelectNode<TargetNodeData> | undefined>(undefined);

  onTargetSelected() {
    this.showTargetDropdown = false;
  }
}
