import {
  ChangeDetectorRef,
  Component,
  effect,
  inject,
  input,
  NgZone,
  OnDestroy,
  OnInit,
  output,
  signal,
  TemplateRef,
} from '@angular/core';
import {
  AbstractControl,
  FormControl,
  FormGroup,
  ReactiveFormsModule,
} from '@angular/forms';
import { CommonModule } from '@angular/common';

import { debounceTime, distinctUntilChanged, Subject, takeUntil } from 'rxjs';

import { ButtonModule } from 'primeng/button';
import { CheckboxModule } from 'primeng/checkbox';
import { ColorPickerModule } from 'primeng/colorpicker';
import { EditorModule } from 'primeng/editor';
import { InputNumberModule } from 'primeng/inputnumber';
import { InputTextModule } from 'primeng/inputtext';
import { MultiSelectModule } from 'primeng/multiselect';
import { PasswordModule } from 'primeng/password';
import { RadioButtonModule } from 'primeng/radiobutton';
import { RatingModule } from 'primeng/rating';
import { SliderModule } from 'primeng/slider';
import { KnobModule } from 'primeng/knob';
import { DatePickerModule } from 'primeng/datepicker';
import { ChipModule } from 'primeng/chip';
import { TextareaModule } from 'primeng/textarea';
import { TooltipModule } from 'primeng/tooltip';
import isEqualWith from 'lodash/isEqualWith';

import {
  DependencyValue,
  FieldDependency,
  FormAction,
  FormFieldConfig,
  FormTemplateContext,
  ListFieldModalConfig,
} from '../../../models/form-field.model';
import { DynamicFormService } from '../../../services/dynamic-form.service';
import { DynamicFieldComponent } from '../dynamic-field/dynamic-field.component';
import { LocalStorageService } from '../../../services/local-storage.service';
import {
  hasProperty,
  isArray,
  isDefined,
  isEmpty,
  isFunction,
  isNullOrUndefined,
  isString,
} from '../../../utils';
import { DialogService, DynamicDialogRef } from 'primeng/dynamicdialog';
import { FilterItem } from '../../../models';
import { MessageService } from 'primeng/api';

@Component({
  selector: 'app-dynamic-form',
  imports: [
    ReactiveFormsModule,
    InputTextModule,
    InputNumberModule,
    TextareaModule,
    DatePickerModule,
    CheckboxModule,
    RadioButtonModule,
    MultiSelectModule,
    RatingModule,
    SliderModule,
    ChipModule,
    ColorPickerModule,
    PasswordModule,
    KnobModule,
    EditorModule,
    ButtonModule,
    TooltipModule,
    DynamicFieldComponent,
    CommonModule,
  ],
  templateUrl: './dynamic-form.component.html',
  styleUrl: './dynamic-form.component.scss',
})
export class DynamicFormComponent implements OnInit, OnDestroy {
  readonly fields = input.required<FormFieldConfig[]>();
  readonly templates = input<Record<string, TemplateRef<FormTemplateContext>>>(
    {},
  );
  readonly sections = input<
    { key: string; title: string; fields: FormFieldConfig[] }[]
  >([]);
  readonly actions = input<FormAction[]>([]);
  hideActions = input<boolean>(false);
  readonly formReady = output<FormGroup>();
  readonly formSubmit = output<FormGroup>();
  readonly formValidityChange = output<boolean>();
  readonly formValueChange = output<Record<string, string | number>>();
  readonly fieldValueChange = output<{
    fieldKey: string;
    value: string | number;
    previousValue: string | number;
  }>();

  private readonly formService = inject(DynamicFormService);
  private readonly formPersistence = inject(LocalStorageService);
  private readonly dialogService = inject(DialogService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly messageService = inject(MessageService);
  private activeDialogRef: DynamicDialogRef | null = null;
  private readonly destroy$ = new Subject<void>();
  private _previousFields: FormFieldConfig[] | undefined;
  private readonly ngZone = inject(NgZone);
  private fieldUpdateInProgress = false;
  private rebuildTimeoutId: ReturnType<typeof setTimeout> | null = null;
  private isDestroyed = false;

  form!: FormGroup;
  collapsedSections = signal<Set<string>>(new Set());
  dynamicFields = signal<FormFieldConfig[]>([]);

  constructor() {
    this.setupFieldChangeEffect();
  }

  /**
   * Sets up the effect to handle field definition changes
   */
  private setupFieldChangeEffect(): void {
    this.ngZone.runOutsideAngular(() => {
      effect(() => {
        const currentFields = this.fields();

        // Skip if update conditions aren't met
        if (this.shouldSkipFieldUpdate(currentFields)) {
          return;
        }

        // Mark update in progress to prevent recursion
        this.fieldUpdateInProgress = true;

        // Store reference to current fields
        this._previousFields = currentFields;

        // Schedule form rebuild on next tick to break potential cycles
        this.scheduleFormRebuild();
      });
    });
  }

  /**
   * Determines if field update should be skipped
   */
  private shouldSkipFieldUpdate(currentFields: FormFieldConfig[]): boolean {
    // Skip if update is already in progress or no form yet
    if (this.fieldUpdateInProgress || !isDefined(this.form)) {
      return true;
    }

    // Skip if fields are the same reference
    if (this._previousFields === currentFields) {
      return true;
    }

    // Skip if fields are the same by deep comparison
    if (this.areFieldsEqual(this._previousFields, currentFields)) {
      this._previousFields = currentFields; // Update reference
      return true;
    }

    return false;
  }

  /**
   * Compares fields for deep equality
   */
  private areFieldsEqual(
    prevFields: FormFieldConfig[] | undefined,
    currentFields: FormFieldConfig[],
  ): boolean {
    if (!prevFields || prevFields.length !== currentFields.length) {
      return false;
    }

    for (let i = 0; i < currentFields.length; i++) {
      if (!this.areFieldsConfigEqual(prevFields[i], currentFields[i])) {
        return false;
      }
    }

    return true; // All fields seem equal
  }

  /**
   * Deep comparison of two FormFieldConfig objects, ignoring functions.
   */
  private areFieldsConfigEqual(
    fieldA: FormFieldConfig,
    fieldB: FormFieldConfig,
  ): boolean {
    // Get keys of non-function properties for fieldA
    const keysA = (Object.keys(fieldA) as (keyof FormFieldConfig)[]).filter(
      (k) => typeof fieldA[k] !== 'function',
    );
    // Get keys of non-function properties for fieldB
    const keysB = (Object.keys(fieldB) as (keyof FormFieldConfig)[]).filter(
      (k) => typeof fieldB[k] !== 'function',
    );

    if (keysA.length !== keysB.length) {
      return false;
    }

    for (const key of keysA) {
      // Use keyof to access properties safely
      const valA = fieldA[key];
      const valB = fieldB[key];

      // Handle nested fields array recursively
      if (key === 'fields' && Array.isArray(valA) && Array.isArray(valB)) {
        // Type assertion needed here as areFieldsEqual expects FormFieldConfig[]
        if (
          !this.areFieldsEqual(
            valA as FormFieldConfig[],
            valB as FormFieldConfig[],
          )
        )
          return false;
        continue; // Skip further comparison for this key
      }

      // Special handling for 'value' property if it's an object, especially for 'eventFilter'
      if (
        key === 'value' &&
        typeof valA === 'object' &&
        valA !== null &&
        typeof valB === 'object' &&
        valB !== null
      ) {
        if (fieldA.type === 'eventFilter' && fieldB.type === 'eventFilter') {
          // For eventFilter values, JSON.stringify is unsafe due to potential circular refs in TreeSelectNode.
          if (valA === valB) {
            // Same object instance
            continue;
          }
          try {
            const filterA = valA as FilterItem;
            const filterB = valB as FilterItem;

            // Compare keys of actor, action, target
            if (
              filterA.actor?.key !== filterB.actor?.key ||
              filterA.action?.key !== filterB.action?.key ||
              filterA.target?.key !== filterB.target?.key
            ) {
              return false;
            }

            // Compare conditions: length and simplified content
            if (filterA.conditions?.length !== filterB.conditions?.length) {
              return false;
            }

            if (
              isDefined(filterA.conditions) &&
              isDefined(filterB.conditions)
            ) {
              for (let i = 0; i < filterA.conditions.length; i++) {
                const condA = filterA.conditions[i];
                const condB = filterB.conditions[i];
                // Compare essential, non-circular parts of conditions
                // Note: condA.values might still be complex. This is a best-effort simplification.
                const simpleCondA = {
                  id: condA.id,
                  type: condA.type,
                  values: condA.values,
                };
                const simpleCondB = {
                  id: condB.id,
                  type: condB.type,
                  values: condB.values,
                };
                try {
                  if (
                    JSON.stringify(simpleCondA) !== JSON.stringify(simpleCondB)
                  ) {
                    return false;
                  }
                } catch (condError) {
                  console.warn(
                    `Could not stringify/compare condition for eventFilter value (key: ${String(key)}, conditionId: ${condA.id}):`,
                    condError,
                  );
                  return false; // Assume different if condition comparison fails
                }
              }
            }
            // If all simplified comparisons pass for eventFilter value, continue to the next key.
            continue;
          } catch (e) {
            console.warn(
              `Error during simplified comparison of eventFilter 'value' for key: ${String(key)}`,
              e,
            );
            return false; // Comparison failed, assume different.
          }
        }
      }

      // Use lodash isEqualWith for deep comparison, handling circular references
      const seen = new WeakMap();
      const isEqual = isEqualWith(valA, valB, (a, _b) => {
        if (typeof a === 'object' && a !== null) {
          if (seen.has(a)) return true;
          seen.set(a, true);
        }
        return undefined; // fallback to default
      });
      if (!isEqual) {
        return false;
      }
    }

    return true;
  }

  /**
   * Schedules form rebuild on next tick
   */
  private scheduleFormRebuild(): void {
    // Clear any pending rebuild to avoid multiple executions
    if (this.rebuildTimeoutId) {
      clearTimeout(this.rebuildTimeoutId);
    }

    this.rebuildTimeoutId = setTimeout(() => {
      // Do not run if the component has been destroyed
      if (this.isDestroyed) {
        return;
      }
      this.ngZone.run(() => {
        try {
          this.rebuildForm();
        } finally {
          // Always reset flag when done
          this.fieldUpdateInProgress = false;
        }
      });
    });
  }

  ngOnInit() {
    this.validateFieldNames(this.fields());
    this.form = this.formService.createFormGroup(this.fields());
    this.dynamicFields.set([...this.fields()]);
    this.processInitialConditionalFields();
    this.loadPersistedState();
    this.handleDependencies();
    this.setupFormPersistence();
    this.formReady.emit(this.form);
    this.form.statusChanges.subscribe((status) => {
      this.formValidityChange.emit(status === 'VALID');
    });

    // Subscribe to form value changes
    this.form.valueChanges.subscribe({
      next: (value) => {
        if (!isEmpty(value)) {
          this.formValueChange.emit(value);
        }
      },
      error: (err) => {
        console.error('Error in form value changes:', err);
      },
    });
  }

  private setupIndividualFieldSubscriptions(): void {
    this.subscribeToFieldChanges(this.form, '');
  }

  private subscribeToFieldChanges(group: FormGroup, parentPath: string): void {
    Object.keys(group.controls).forEach((key) => {
      const control = group.get(key) as FormGroup | FormControl;
      const fullPath = parentPath ? `${parentPath}.${key}` : key;

      if (control instanceof FormGroup) {
        // Recursively subscribe to nested groups
        this.subscribeToFieldChanges(control, fullPath);
      } else {
        // Subscribe to individual field changes
        let previousValue = control.value;
        control.valueChanges
          .pipe(takeUntil(this.destroy$))
          .subscribe((newValue) => {
            this.fieldValueChange.emit({
              fieldKey: fullPath,
              value: newValue,
              previousValue: previousValue,
            });
            previousValue = newValue;
          });
      }
    });
  }

  ngOnDestroy() {
    this.isDestroyed = true;
    if (this.rebuildTimeoutId) {
      clearTimeout(this.rebuildTimeoutId);
    }
    this.destroy$.next();
    this.destroy$.complete();
    this.activeDialogRef?.close();
  }

  validateFieldNames(fields: FormFieldConfig[]): void {
    fields.forEach((field) => {
      // Check nested fields in groups
      if (field.type === 'group' && field.fields) {
        this.validateFieldNames(field.fields);
      }

      // Check fields in conditions
      if (isDefined(field.conditional) && field.conditions) {
        Object.values(field.conditions).forEach((conditionalFields) => {
          this.validateFieldNames(conditionalFields);
        });
      }
    });
  }

  private rebuildForm(): void {
    // Store current values to preserve them if possible
    const currentValues = isDefined(this.form) ? { ...this.form.value } : {};

    // Clean up existing form controls
    if (isDefined(this.form)) {
      Object.keys(this.form.controls).forEach((key) => {
        this.form.removeControl(key);
      });
    }

    // Rebuild the form with new fields
    this.validateFieldNames(this.fields());

    // Create new form controls
    const newFields = this.fields();
    const formControls = this.formService.createFormGroup(newFields);

    // Add all controls to the existing form
    Object.keys(formControls.controls).forEach((key) => {
      this.form.addControl(key, formControls.get(key));
    });

    // Reset dynamic fields
    this.dynamicFields.set([...newFields]);

    // Re-process conditional fields
    this.processInitialConditionalFields();

    // Restore compatible values

    this.restoreFormValues(currentValues);

    // Re-wire dependencies
    this.handleDependencies();

    setTimeout(() => {
      this.setupIndividualFieldSubscriptions();
    }, 0);

    // Emit the rebuilt form
    this.formReady.emit(this.form);
  }

  // Helper to restore compatible values
  private restoreFormValues(values: Record<string, string | number>): void {
    if (isNullOrUndefined(values) || isNullOrUndefined(this.form)) return;

    // First pass: handle top-level fields
    Object.keys(values).forEach((key) => {
      const control = this.form.get(key);
      if (control && !isEmpty(values[key])) {
        // Handle nested objects by checking if the control is a FormGroup
        if (typeof values[key] === 'object' && control instanceof FormGroup) {
          this.restoreNestedValues(control, values[key]);
        } else {
          control.setValue(values[key], { emitEvent: false });
        }
      }
    });

    // Force validation on restored values
    this.form.updateValueAndValidity();
  }

  private restoreNestedValues(
    group: FormGroup,
    values: Record<string, string | number>,
  ): void {
    if (isNullOrUndefined(values)) return;

    // Only set values for controls that actually exist in the form group
    Object.keys(values).forEach((key) => {
      const control = group.get(key);

      if (control) {
        // If this is another nested group, recurse
        if (typeof values[key] === 'object' && control instanceof FormGroup) {
          this.restoreNestedValues(control, values[key]);
        } else {
          // Otherwise set the value safely
          control.setValue(values[key], { emitEvent: false });
        }
      }
    });
  }

  private setupFormPersistence() {
    this.form.valueChanges
      .pipe(debounceTime(500), distinctUntilChanged(), takeUntil(this.destroy$))
      .subscribe(() => {
        this.saveState();
      });
  }

  private saveState() {
    for (const section of this.sections()) {
      for (const field of section.fields) {
        if (isDefined(field.persistKey)) {
          const value = this.form.get(field.key)?.value;
          if (value !== undefined) {
            this.formPersistence.setItem(field.persistKey, value);
          }
        }
      }
    }
  }

  private handleDependencies(): void {
    for (const section of this.sections()) {
      for (const field of section.fields) {
        if (field.dependencies) {
          this.setupFieldDependencies(field);
        }
      }
    }
  }

  private setupFieldDependencies(field: FormFieldConfig): void {
    for (const dep of field.dependencies || []) {
      this.form
        .get(dep.field)
        ?.valueChanges.pipe(takeUntil(this.destroy$))
        .subscribe((value) => this.handleDependencyAction(field, dep, value));
    }
  }

  private handleDependencyAction(
    field: FormFieldConfig,
    dep: FieldDependency,
    value: DependencyValue,
  ): void {
    if (value === dep.value) {
      switch (dep.action) {
        case 'show':
          field.visible = true;
          break;
        case 'hide':
          field.visible = false;
          break;
        case 'enable':
          this.form.get(field.key)?.enable();
          break;
        case 'disable':
          this.form.get(field.key)?.disable();
          break;
      }
    }
  }

  private loadPersistedState() {
    for (const section of this.sections()) {
      for (const field of section.fields) {
        if (isDefined(field.persistKey)) {
          const savedValue = this.formPersistence.getItem(field.persistKey);
          if (savedValue !== null) {
            this.form.get(field.key)?.setValue(savedValue);
          }
        }
      }
    }
  }

  handleAction(action: FormAction): void {
    if (action.action) {
      action.action(this.form);
    }
  }

  isDisabled(action: FormAction): boolean {
    if (isDefined(action.disabled)) {
      return isFunction<boolean, [FormGroup]>(action.disabled)
        ? action.disabled(this.form)
        : !!action.disabled;
    }
    return false;
  }

  isVisible(action: FormAction): boolean {
    if (isDefined(action.visible)) {
      return isFunction<boolean, [FormGroup]>(action.visible)
        ? action.visible(this.form)
        : !!action.visible;
    }
    return true;
  }

  toggleSection(sectionId: string): void {
    const current = this.collapsedSections();
    if (current.has(sectionId)) {
      current.delete(sectionId);
    } else {
      current.add(sectionId);
    }
    this.collapsedSections.set(new Set(current)); // Update signal
  }

  isSectionCollapsed(sectionId: string): boolean {
    return this.collapsedSections().has(sectionId);
  }

  isFieldVisible(field: FormFieldConfig): boolean {
    return field.visible !== false;
  }

  getFieldControl(key: string): FormControl {
    return this.form.get(key) as FormControl;
  }

  hasError(fieldName: string, group: FormGroup): boolean {
    const control = group.get(fieldName);
    return control
      ? control.invalid && (control.dirty || control.touched)
      : false;
  }

  getErrorMessage(fieldName: string, group: FormGroup): string {
    const control = group.get(fieldName);
    if (!isDefined(control) || !isDefined(control.errors)) return '';

    const errorKeys = Object.keys(control.errors);
    if (!isArray(errorKeys) || errorKeys.length === 0) return '';

    const firstError = errorKeys[0];
    if (!hasProperty(control.errors, firstError)) return '';

    const errorValue = control.errors[firstError];
    return isString(errorValue) ? errorValue : '';
  }

  onSubmit() {
    if (this.form.valid) {
      this.formSubmit.emit(this.form.value);
    }
  }

  getFieldWithPath(
    field: FormFieldConfig,
    parentPath: string,
  ): FormFieldConfig {
    return {
      ...field,
      key: this.getFullPath(field.key, parentPath),
    };
  }

  getFullPath(key: string, parentPath: string): string {
    return parentPath ? `${parentPath}.${key}` : key;
  }

  /**
   * Handles field changes that might trigger conditional fields
   * Uses the ConditionalFieldService for processing
   */
  onFieldChange(
    field: FormFieldConfig,
    value: string | number,
    parentPath = '',
  ): void {
    // Early return if conditions not met
    if (
      !(field.conditional ?? false) ||
      !isDefined(field.conditions) ||
      !isDefined(field.conditions[value])
    ) {
      return;
    }

    try {
      // Step 1: Prepare field maps
      const { conditionalFields, previousFields } = this.prepareFieldMaps(
        field,
        value,
      );

      // Step 2: Handle group preservation and field removal
      const { fieldsToRemove, groupsToPreserve } = this.handleFieldPreservation(
        conditionalFields,
        previousFields,
      );

      // Step 3: Process form structure changes
      this.processFormStructureChanges(
        conditionalFields,
        previousFields,
        fieldsToRemove,
        groupsToPreserve,
        parentPath,
      );

      // Step 4: Update dynamic fields
      this.updateDynamicFieldsWithConditionals(
        conditionalFields,
        fieldsToRemove,
        groupsToPreserve,
        parentPath,
      );

      // Step 5: Process nested conditionals
      this.processNestedConditionalTriggers(conditionalFields, parentPath);
    } catch (error) {
      console.error(...'Error in onFieldChange:', error);
    }
  }

  /**
   * Prepare maps of conditional and previous fields
   */
  private prepareFieldMaps(
    field: FormFieldConfig,
    value: string | number,
  ): {
    conditionalFields: FormFieldConfig[];
    previousFields: FormFieldConfig[];
  } {
    // Get fields for new condition
    const conditionalFields = field.conditions?.[value] ?? [];

    // Get fields for all other conditions
    const previousFields = Object.entries(field.conditions ?? {})
      .filter(([k]) => k !== String(value))
      .flatMap(([_, fields]) => fields);

    return { conditionalFields, previousFields };
  }

  /**
   * Handle group preservation and collect fields to remove
   */
  private handleFieldPreservation(
    conditionalFields: FormFieldConfig[],
    previousFields: FormFieldConfig[],
  ): {
    fieldsToRemove: Set<string>;
    groupsToPreserve: Set<string>;
  } {
    const fieldsToRemove = new Set<string>();
    const groupsToPreserve = new Set<string>();

    // Create lookup map for new fields
    const newFieldsMap = new Map<string, FormFieldConfig>();
    conditionalFields.forEach((f) => newFieldsMap.set(f.name, f));

    // Find groups to preserve and fields to remove
    previousFields.forEach((oldField) => {
      if (oldField.type === 'group' && newFieldsMap.has(oldField.name)) {
        groupsToPreserve.add(oldField.name);
      }
      this.collectFieldsToRemove(oldField, fieldsToRemove);
    });

    return { fieldsToRemove, groupsToPreserve };
  }

  /**
   * Process form structure changes
   */
  private processFormStructureChanges(
    conditionalFields: FormFieldConfig[],
    previousFields: FormFieldConfig[],
    fieldsToRemove: Set<string>,
    groupsToPreserve: Set<string>,
    parentPath: string,
  ): void {
    // Remove fields that should be completely removed
    previousFields.forEach((fieldToRemove) => {
      if (
        !(
          fieldToRemove.type === 'group' &&
          groupsToPreserve.has(fieldToRemove.name)
        )
      ) {
        this.removeFieldFromForm(fieldToRemove, parentPath);
      }
    });

    // Build conditional form group hierarchy
    this.buildConditionalFormGroupHierarchy(conditionalFields, parentPath);

    // Clear shared groups but keep structure
    groupsToPreserve.forEach((groupName) => {
      const groupPath = parentPath ? `${parentPath}.${groupName}` : groupName;
      this.clearFormGroup(groupPath);
    });
  }

  /**
   * Update dynamic fields with conditional fields
   */
  private updateDynamicFieldsWithConditionals(
    conditionalFields: FormFieldConfig[],
    fieldsToRemove: Set<string>,
    groupsToPreserve: Set<string>,
    parentPath: string,
  ): void {
    const currentFields = this.dynamicFields();

    // Filter out fields that should be removed
    const updatedFields = currentFields.filter(
      (f) => !fieldsToRemove.has(f.name) || groupsToPreserve.has(f.name),
    );

    // Create array for final fields
    const finalFields: FormFieldConfig[] = [];

    // Process preserved groups and existing fields
    for (const field of updatedFields) {
      if (groupsToPreserve.has(field.name) && field.type === 'group') {
        this.updateGroupField(field, conditionalFields, finalFields);
      } else {
        finalFields.push(field);
      }
    }

    // Add new fields not already present
    this.addNewFields(conditionalFields, updatedFields, finalFields);

    // Update dynamic fields signal
    this.dynamicFields.set(finalFields);

    // Add fields to form structure
    this.addFieldsToForm(conditionalFields, parentPath);
  }

  /**
   * Update a group field with new content
   */
  private updateGroupField(
    groupField: FormFieldConfig,
    conditionalFields: FormFieldConfig[],
    finalFields: FormFieldConfig[],
  ): void {
    // Find new version of this group
    const newVersion = conditionalFields.find(
      (f) => f.name === groupField.name,
    );

    if (newVersion) {
      // Use new version but keep metadata
      finalFields.push({
        ...groupField,
        fields: [...(newVersion.fields || [])],
      });
    } else {
      finalFields.push({ ...groupField, fields: [] });
    }
  }

  /**
   * Add new fields not already present
   */
  private addNewFields(
    newFields: FormFieldConfig[],
    existingFields: FormFieldConfig[],
    finalFields: FormFieldConfig[],
  ): void {
    for (const newField of newFields) {
      if (!existingFields.some((f) => f.name === newField.name)) {
        finalFields.push(newField);
      }
    }
  }

  /**
   * Process nested conditional triggers
   */
  private processNestedConditionalTriggers(
    fields: FormFieldConfig[],
    parentPath: string,
  ): void {
    fields.forEach((field) => {
      if ((field.conditional ?? false) && field.conditions) {
        const defaultValue = field.value as string | number;

        if (
          isDefined(defaultValue) &&
          isDefined(field.conditions[defaultValue])
        ) {
          const childPath = parentPath
            ? `${parentPath}.${field.name}`
            : field.name;

          // Use setTimeout to avoid stack overflow with deep nesting
          setTimeout(() => {
            this.onFieldChange(field, defaultValue, childPath);
          }, 0);
        }
      }
    });
  }

  /**
   * Add fields to form structure
   */
  private addFieldsToForm(fields: FormFieldConfig[], parentPath: string): void {
    fields.forEach((field) => {
      if (field.type === 'group' && field.fields) {
        const fullPath = parentPath
          ? `${parentPath}.${field.name}`
          : field.name;
        this.populateFormGroup(field.fields, fullPath);
      } else if (!this.form.get(field.name)) {
        this.addSingleFieldToForm(field, parentPath);
      }
    });
  }

  /**
   * Add a single field to the form
   */
  private addSingleFieldToForm(
    field: FormFieldConfig,
    parentPath: string,
  ): void {
    const control = this.createControl(field);

    if (parentPath) {
      const parentGroup = this.form.get(parentPath);
      if (parentGroup instanceof FormGroup) {
        parentGroup.addControl(field.name, control);
      }
    } else {
      this.form.addControl(field.name, control);
    }
  }
  /**
   * Build the entire form group hierarchy for conditional fields
   * This method creates all necessary form groups before populating them with fields
   */
  private buildConditionalFormGroupHierarchy(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    // First process: Find and create all group hierarchies
    const groupsToCreate = new Map<
      string,
      {
        path: string;
        parentPath?: string;
        field: FormFieldConfig;
      }
    >();

    // Collect all groups and their paths
    this.collectGroupsAndPaths(fields, parentPath, groupsToCreate);

    // Create all groups in hierarchical order (parents first)
    const paths = Array.from(groupsToCreate.keys()).sort(
      (a, b) => a.split('.').length - b.split('.').length,
    );

    // First pass: Create all groups in order of depth
    for (const path of paths) {
      const groupInfo = groupsToCreate.get(path);
      if (!groupInfo) {
        continue;
      }
      const { parentPath } = groupInfo;
      this.ensureGroupExists(path, parentPath);
    }

    // Second pass: Recursively handle any nested conditional fields inside groups
    for (const { path, field } of groupsToCreate.values()) {
      if (field.fields) {
        field.fields.forEach((childField) => {
          if ((childField.conditional ?? false) && childField.conditions) {
            const defaultValue = childField.value as string | number;
            if (
              isDefined(defaultValue) &&
              isDefined(childField.conditions[defaultValue])
            ) {
              const conditionalFields =
                childField.conditions[defaultValue] ?? [];
              this.buildConditionalFormGroupHierarchy(conditionalFields, path);
            }
          }
        });
      }
    }
  }

  /**
   * Collect all groups and their paths recursively
   */
  private collectGroupsAndPaths(
    fields: FormFieldConfig[],
    parentPath: string,
    groupMap: Map<
      string,
      { path: string; parentPath?: string; field: FormFieldConfig }
    >,
  ): void {
    fields.forEach((field) => {
      if (field.type === 'group') {
        const path = parentPath ? `${parentPath}.${field.name}` : field.name;

        // Add this group to the map
        groupMap.set(path, { path, parentPath, field });

        // Recursively collect nested groups
        if (field.fields) {
          this.collectGroupsAndPaths(field.fields, path, groupMap);
        }
      }
    });
  }

  /**
   * Ensure a form group exists at the specified path, creating it if needed
   */
  private ensureGroupExists(path: string, parentPath?: string): FormGroup {
    const existingGroup = this.form.get(path);
    if (existingGroup instanceof FormGroup) {
      return existingGroup;
    }

    // Group doesn't exist, create it
    const pathParts = path.split('.');
    const groupName = pathParts[pathParts.length - 1];

    if (isNullOrUndefined(parentPath)) {
      // Top-level group
      const group = new FormGroup({});
      this.form.addControl(groupName, group);
      return group;
    } else {
      // Nested group, ensure parent exists first
      const parentGroup = this.ensureGroupExists(parentPath);
      const group = new FormGroup({});
      parentGroup.addControl(groupName, group);
      return group;
    }
  }

  /**
   * Clear all controls from a form group but preserve the group itself
   */
  private clearFormGroup(groupPath: string): void {
    // First ensure the group exists
    this.ensureGroupExists(groupPath);

    const group = this.form.get(groupPath);
    if (group instanceof FormGroup) {
      // Save the control names before removing them
      const controlNames = Object.keys(group.controls);

      // Remove all controls from the group
      controlNames.forEach((key) => {
        group.removeControl(key);
      });
    }
  }

  /**
   * Populate a form group with fields recursively
   */
  private populateFormGroup(
    fields: FormFieldConfig[],
    groupPath: string,
  ): void {
    // First ensure the group exists
    const group = this.ensureGroupExists(groupPath);

    try {
      // First add all nested groups to ensure hierarchy
      fields.forEach((field) => {
        if (field.type === 'group') {
          // Create nested group if it doesn't exist
          const childGroupPath = `${groupPath}.${field.name}`;
          this.ensureGroupExists(childGroupPath, groupPath);
        }
      });

      // Then add all fields
      fields.forEach((field) => {
        if (field.type === 'group' && field.fields) {
          // Group exists, now populate it
          this.populateFormGroup(field.fields, `${groupPath}.${field.name}`);
        } else if (!group.get(field.name)) {
          // Add regular field
          group.addControl(field.name, this.createControl(field));
        }
      });
    } catch (error) {
      console.error(`Error populating group ${groupPath}:`, error);
    }
  }

  /**
   * Build the entire form group hierarchy first before populating fields
   */
  private buildFormGroupHierarchy(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    // Handle regular groups
    this.createGroupsFromFields(fields);

    // Handle conditional groups
    this.createConditionalGroups(fields, parentPath);
  }

  /**
   * Create form groups for all group-type fields (including nested groups)
   */
  private createGroupsFromFields(fields: FormFieldConfig[]): void {
    for (const field of fields) {
      if (field.type !== 'group') {
        continue;
      }

      // Skip if group already exists
      if (this.form.get(field.name)) {
        continue;
      }

      const control = this.createControl(field);
      this.form.addControl(field.name, control);

      // Recursively process nested groups
      if (field.fields) {
        this.createGroupsFromFields(field.fields);
      }
    }
  }

  /**
   * Create form groups from conditional fields with default values
   */
  private createConditionalGroups(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    for (const field of fields) {
      // Skip non-conditional fields or fields without conditions or value
      if (
        !(field.conditional ?? false) ||
        !field.conditions ||
        !isDefined(field.value)
      ) {
        continue;
      }

      const value = field.value as string | number;

      // Skip if no condition matches the value
      if (!isDefined(field.conditions[value])) {
        continue;
      }

      // Process conditional fields that are groups
      const conditionalFields = field.conditions[value] ?? [];
      for (const condField of conditionalFields) {
        if (condField.type === 'group') {
          this.buildFormGroupHierarchy([condField], parentPath);
        }
      }
    }
  }

  /**
   * Process all fields recursively to find and initialize nested conditional fields
   */
  private processNestedFieldsForInitialConditions(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    fields.forEach((field) => {
      const fieldPath = parentPath ? `${parentPath}.${field.name}` : field.name;

      // If this is a conditional field with a default value
      if (
        (field.conditional ?? false) &&
        field.conditions &&
        isDefined(field.value)
      ) {
        const defaultValue = field.value as string | number;
        if (isDefined(field.conditions[defaultValue])) {
          // Process this field's conditions
          this.onFieldChange(field, defaultValue, parentPath);
        }
      }

      // If this is a group, process its fields
      if (field.type === 'group' && field.fields) {
        this.processNestedFieldsForInitialConditions(field.fields, fieldPath);
      }
    });
  }

  /**
   * Recursively collect a field and all its descendants' names
   */
  private collectFieldsToRemove(
    field: FormFieldConfig,
    fieldNames: Set<string>,
  ): void {
    // Add this field
    fieldNames.add(field.name);

    // Process conditional fields
    if ((field.conditional ?? false) && field.conditions) {
      Object.values(field.conditions)
        .flat()
        .forEach((condField) =>
          this.collectFieldsToRemove(condField, fieldNames),
        );
    }

    // Process group fields
    if (field.type === 'group' && field.fields) {
      field.fields.forEach((childField) =>
        this.collectFieldsToRemove(childField, fieldNames),
      );
    }
  }

  /**
   * Recursively remove a field and all its nested fields from the form
   */
  private removeFieldFromForm(field: FormFieldConfig, parentPath = ''): void {
    // Create the full path to this field
    const fullPath = parentPath ? `${parentPath}.${field.name}` : field.name;

    // IMPORTANT: First process conditional fields at this level
    if ((field.conditional ?? false) && field.conditions) {
      // Get all possible conditional fields from all values
      const allConditionalFields = Object.values(field.conditions).flat();

      // Remove them all (we don't know which condition was active)
      allConditionalFields.forEach((condField) => {
        // Use the current field's full path as parent for its conditions
        this.removeFieldFromForm(condField, fullPath);
      });
    }

    // Then process group fields
    if (field.type === 'group' && field.fields) {
      // Remove all children first (from end to start to avoid index issues)
      [...field.fields].reverse().forEach((childField) => {
        this.removeFieldFromForm(childField, fullPath);
      });
    }

    // Finally, remove this field itself
    try {
      if (!parentPath) {
        // For top-level fields
        if (this.form.get(field.name)) {
          this.form.removeControl(field.name);
        }
      } else {
        // For nested fields, get the parent
        const parent = this.form.get(parentPath);
        if (parent instanceof FormGroup) {
          if (parent.get(field.name)) {
            parent.removeControl(field.name);
          }
        }
      }
    } catch (error) {
      console.error(`Error removing field ${fullPath}:`, error);
    }
  }

  /**
   * Create an appropriate form control/group for a field
   */
  private createControl(field: FormFieldConfig): AbstractControl {
    if (field.type === 'group') {
      // Create a form group for group fields
      const group = new FormGroup({});

      // Add controls for all child fields
      (field.fields || []).forEach((childField) => {
        group.addControl(childField.name, this.createControl(childField));
      });

      return group;
    } else {
      // Create a regular form control for other fields
      const validators = field.validations?.map((v) => v.validator) || [];
      return new FormControl(field.value, validators);
    }
  }

  /**
   * Process initial conditional fields based on default values
   */
  private processInitialConditionalFields(): void {
    // First: build the complete form group hierarchy
    this.buildFormGroupHierarchy(this.fields());

    // Then: Process top-level conditional fields
    this.processConditionalFieldsAtLevel(this.fields());
  }

  /**
   * Process conditional fields at a given level of the form structure
   * @param fields The fields to process at this level
   * @param parentPath Optional parent path for nested fields
   */
  private processConditionalFieldsAtLevel(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    // Find all conditional fields at this level
    const conditionalFields = fields.filter(
      (field) => (field.conditional ?? false) && isDefined(field.conditions),
    );

    // Process each conditional field
    conditionalFields.forEach((field) => {
      // Get full path for this field
      const fullPath = parentPath ? `${parentPath}.${field.name}` : field.name;

      // Get control for this path
      const control = this.form.get(fullPath);
      if (!control) {
        return;
      }

      const initialValue = control.value;
      if (isDefined(initialValue) && field.conditions?.[initialValue]) {
        // Add the conditional fields based on this value
        const conditionalFieldsToAdd = field.conditions[initialValue] ?? [];
        this.addConditionalFields(conditionalFieldsToAdd, parentPath);

        // Recursively process any nested conditionals
        this.processNestedConditionalFields(conditionalFieldsToAdd, parentPath);
      }
    });

    // Second pass: Process group fields to find deeper conditionals
    fields.forEach((field) => {
      if (field.type === 'group' && field.fields) {
        const groupPath = parentPath
          ? `${parentPath}.${field.name}`
          : field.name;
        this.processConditionalFieldsAtLevel(field.fields, groupPath);
      }
    });
  }

  /**
   * Add a single field to the form with proper hierarchy
   */
  private addFieldToForm(field: FormFieldConfig, parentPath = ''): void {
    const fullPath = parentPath ? `${parentPath}.${field.name}` : field.name;

    // Check if control already exists
    if (this.form.get(fullPath)) {
      return;
    }

    // Create the control
    const control = this.createControl(field);

    // Add to the appropriate parent
    if (!parentPath) {
      this.form.addControl(field.name, control);
    } else {
      const parent = this.form.get(parentPath);
      if (parent instanceof FormGroup) {
        parent.addControl(field.name, control);
      } else {
        console.warn(`Parent FormGroup not found at path: ${parentPath}`);
        return;
      }
    }

    // If this field has conditional fields with default values, schedule their processing
    if (
      (field.conditional ?? false) &&
      field.conditions &&
      isDefined(field.value)
    ) {
      const defaultValue = field.value as string | number;
      if (isDefined(field.conditions[defaultValue])) {
        setTimeout(() => {
          this.onFieldChange(field, defaultValue, parentPath);
        }, 0);
      }
    }
  }

  /**
   * Add conditional fields to both the form model and dynamic fields
   */
  private addConditionalFields(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    // First add to form model to create controls
    fields.forEach((field) => {
      this.addFieldToForm(field, parentPath);
    });

    // Then add to dynamic fields for rendering
    this.dynamicFields.update((currentFields) => {
      const fieldMap = new Map(currentFields.map((f) => [f.name, f]));
      const newFields = [...currentFields];

      fields.forEach((field) => {
        if (!fieldMap.has(field.name)) {
          newFields.push(field);
        }
      });

      return newFields;
    });

    // Process nested conditionals after both form controls and dynamic fields are ready
    setTimeout(() => {
      this.processNestedConditionalFields(fields, parentPath);
    }, 0);
  }

  /**
   * Process nested conditional fields recursively
   */
  private processNestedConditionalFields(
    fields: FormFieldConfig[],
    parentPath = '',
  ): void {
    // Use setTimeout to ensure form controls are fully initialized
    setTimeout(() => {
      fields.forEach((field) => {
        const fieldPath = parentPath
          ? `${parentPath}.${field.name}`
          : field.name;

        // If this is a group, process its fields
        if (field.type === 'group' && field.fields) {
          this.processConditionalFieldsAtLevel(field.fields, fieldPath);
        }

        // If this is a conditional field, process it
        if ((field.conditional ?? false) && field.conditions) {
          const control = this.form.get(fieldPath);
          if (control) {
            const value = control.value as string | number;
            if (isDefined(value) && isDefined(field.conditions[value])) {
              // Add these conditional fields
              this.addConditionalFields(field.conditions[value], fieldPath);

              // Process these fields recursively
              this.processNestedConditionalFields(
                field.conditions[value],
                fieldPath,
              );
            }
          }
        }
      });
    }, 0);
  }

  /**
   * Helper method to safely check if a control is a FormGroup
   */
  isFormGroup(control: AbstractControl | null): boolean {
    return control instanceof FormGroup;
  }

  handleAddItem(event: { field: FormFieldConfig }): void {
    const { field } = event;
    const modalConfig = field.props?.modalConfig;
    if (!modalConfig) {
      console.error(`Modal config missing for list field: ${field.key}`);
      return;
    }

    const initialData = modalConfig.defaultItemProvider
      ? modalConfig.defaultItemProvider()
      : {};
    this.openListItemModal(field, modalConfig, initialData);
  }

  handleEditItem(event: {
    field: FormFieldConfig;
    item: unknown;
    index: number;
  }): void {
    const { field, item, index } = event;
    const modalConfig = field.props?.modalConfig;
    if (!modalConfig) {
      console.error(`Modal config missing for list field: ${field.key}`);
      return;
    }
    this.openListItemModal(field, modalConfig, item, index);
  }

  async handleDeleteItem(event: {
    field: FormFieldConfig;
    index: number;
  }): Promise<void> {
    const { field, index } = event;
    const control = this.form.get(field.key);
    if (!control) return;

    const currentValue = control.value;
    if (!isArray(currentValue)) return;

    const itemToDelete = currentValue[index];
    const modalConfig = field.props?.modalConfig;

    let proceedWithDefaultDeletion = true;

    if (modalConfig && isFunction(modalConfig.onDeleteItem)) {
      try {
        const result = await modalConfig.onDeleteItem(
          itemToDelete,
          index,
          field,
        );
        if (result === false) {
          proceedWithDefaultDeletion = false;
        }
      } catch (error) {
        console.error(
          `Error in custom onDeleteItem for field ${field.key}:`,
          error,
        );
        // Optionally, decide if default deletion should be skipped on custom handler error
        // For now, we'll let it proceed unless explicitly told not to.
      }
    }

    if (proceedWithDefaultDeletion) {
      const updatedValue = [...currentValue];
      updatedValue.splice(index, 1); // Remove item at index
      control.setValue(updatedValue);
      control.markAsDirty();
      control.updateValueAndValidity();
      this.cdr.markForCheck(); // Trigger change detection
    }

    this.messageService.add({
      severity: 'info',
      summary: 'Item Deleted',
      detail: 'Click "Save" to save your changes.',
    });
  }

  // --- Modal Opening Logic ---

  private openListItemModal(
    field: FormFieldConfig,
    modalConfig: ListFieldModalConfig,
    itemData: unknown, // Data for the item being added/edited
    editIndex?: number, // Index if editing, undefined if adding
  ): void {
    const control = this.form.get(field.key) as FormControl; // Cast for type safety
    if (isNullOrUndefined(control)) return;

    const modalFields = isFunction(modalConfig.modalFields)
      ? modalConfig.modalFields(itemData)
      : modalConfig.modalFields;

    let modalFieldsFn: (() => FormFieldConfig[]) | undefined;
    if (isFunction(modalConfig.modalFields)) {
      const fn = modalConfig.modalFields as (
        item?: unknown,
      ) => FormFieldConfig[];
      modalFieldsFn = () => fn(itemData);
    }

    let modalHeader: string;
    if (isFunction(modalConfig.modalTitle)) {
      modalHeader = modalConfig.modalTitle(itemData);
    } else {
      // If it's a static string, modify it based on whether we're editing or adding
      const baseTitle = modalConfig.modalTitle;
      if (editIndex !== undefined) {
        // If we're editing, replace "Add" with "Edit" if present, otherwise prepend "Edit"
        modalHeader =
          baseTitle.replace(/^Add\s+/, 'Edit ') === baseTitle
            ? `Edit ${baseTitle}`
            : baseTitle.replace(/^Add\s+/, 'Edit ');
      } else {
        modalHeader = baseTitle;
      }
    }

    // Close previous dialog if any
    this.activeDialogRef?.close();

    this.activeDialogRef = this.dialogService.open(
      modalConfig.modalComponent,
      // ----------------------------------------
      {
        header: modalHeader,
        contentStyle: {
          'max-height': '80vh',
          overflow: 'auto',
          'min-width': '40rem',
        },
        style: { width: 'auto' },
        baseZIndex: 10000,
        focusOnShow: false,
        modal: true,
        data: {
          fields: modalFields,
          fieldsFn: modalFieldsFn,
          initialData: itemData,
          subHeader: modalConfig.subHeader,
          actions: [
            {
              label: 'Cancel',
              severity: 'secondary',
              type: 'cancel',
              action: () => this.activeDialogRef?.close(),
            },
            {
              label: editIndex !== undefined ? 'Update' : 'Add',
              severity: 'primary',
              type: 'submit',
              action: async (submittedData: FormGroup) => {
                // Make this async
                if (this.activeDialogRef) {
                  // Ensure dialogRef is available
                  if (isFunction(modalConfig.onSubmit)) {
                    try {
                      // Pass TListItem and DynamicDialogRef
                      const result = await modalConfig.onSubmit(
                        submittedData.value,
                        this.activeDialogRef,
                      );
                      // If onSubmit returns false, or a promise resolving to false, don't close.
                      // If onSubmit itself closes the dialog, this.activeDialogRef might become null.
                      if (result !== false && isDefined(this.activeDialogRef)) {
                        // If onSubmit hasn't closed the dialog and hasn't returned false,
                        // close with the original submittedData.
                        // The onSubmit function can use dialogRef.close(customResult) for different data.
                        this.activeDialogRef.close(submittedData);
                      }
                      // If result is false, or if onSubmit closed the dialog, do nothing further here.
                    } catch (error) {
                      console.error('Error in modalConfig.onSubmit:', error);
                      // Dialog remains open on error by default, unless onSubmit handles it.
                    }
                  } else {
                    this.activeDialogRef.close(submittedData);
                  }
                }
              },
            },
          ],
        },
      },
    );

    // Handle modal close event to get submitted data
    this.activeDialogRef.onClose.subscribe((submittedItemData: unknown) => {
      this.activeDialogRef = null; // Clear ref
      if (isDefined(submittedItemData)) {
        // Check if data was actually submitted (not just closed)
        this.handleModalSubmit(submittedItemData, control, field, editIndex);
      }
    });
  }

  // --- Modal Submit Handler ---
  // Now receives data from the closed dialog
  private handleModalSubmit(
    submittedItemData: unknown,
    listControl: FormControl,
    field: FormFieldConfig,
    editIndex?: number,
  ): void {
    const currentValue = listControl.value;
    const updatedValue = isArray(currentValue) ? [...currentValue] : [];
    if (editIndex !== undefined) {
      // Editing existing item
      updatedValue[editIndex] = submittedItemData;
    } else {
      // Adding new item
      updatedValue.push(submittedItemData);
    }

    listControl.setValue(updatedValue);
    listControl.markAsDirty();
    listControl.updateValueAndValidity();
    const modalConfig = field.props?.modalConfig as ListFieldModalConfig;
    if (!(modalConfig?.hideSaveNotification ?? false)) {
      const summary = editIndex !== undefined ? 'Item Updated' : 'Item Added';
      this.messageService.add({
        severity: 'info',
        summary: summary,
        detail: 'Click "Save" to save your changes.',
        life: 5000,
      });
    }

    this.cdr.markForCheck(); // Trigger change detection
  }

  warn = console.warn;
}
