/**
 * Get display name for a specific condition type
 */
export function getConditionTypeDisplayName(conditionType: string): string {
  return conditionTypeMapping[conditionType]?.name || conditionType;
}

/**
 * Get text pattern for a specific condition type
 */
export function getConditionTypeText(conditionType: string): string {
  return conditionTypeMapping[conditionType]?.text || '';
}

/**
 * Get specific mode text for a condition type
 */
export function getConditionModeText(
  conditionType: string,
  modeKey: string,
): string {
  return conditionTypeMapping[conditionType]?.modes?.[modeKey] ?? modeKey;
}

/**
 * Get specific field text for a condition type
 */
export function getConditionFieldText(
  conditionType: string,
  fieldKey: string,
): string {
  return conditionTypeMapping[conditionType]?.fields?.[fieldKey] ?? fieldKey;
}
