import { inject, Pipe, PipeTransform } from '@angular/core';
import { formatDistanceToNow, isValid } from 'date-fns';
import { MeasurementDateTimeService } from '../services/measurement-units/measurement-date-time.service';

@Pipe({
  name: 'timeAgo',
  pure: true,
})
export class TimeAgoPipe implements PipeTransform {
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );

  transform(value: string, _timestamp?: number): string {
    if (!value) return '';

    try {
      // Use the service to create a proper UTC date
      const utcDate =
        this.measurementDateTimeService.createDateAssumingUtc(value);

      if (!isValid(utcDate)) {
        return 'Invalid date';
      }

      return formatDistanceToNow(utcDate, { addSuffix: true });
    } catch (error) {
      console.error('Error in TimeAgoPipe:', error);
      return 'Invalid date';
    }
  }
}
