import {
  Component,
  ChangeDetectionStrategy,
  model,
  input,
  output,
  OnInit,
  inject,
  ChangeDetectorRef,
  effect,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { DialogModule } from 'primeng/dialog';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';
import { InputNumberModule } from 'primeng/inputnumber';
import { CheckboxModule } from 'primeng/checkbox';

import { isDefined, isNullOrUndefined } from '../../../../utils';
import { SelectModule } from 'primeng/select';
import { SearchableTreeComponent } from '../../../searchable-tree/searchable-tree.component';
import { ClientDataService } from '../../../../../features/clients/services/client-data.service';
import { DatePickerModule } from 'primeng/datepicker';
import { ConditionPart, FilterCondition } from '../../../../models';
import { EventFilterConditionService } from '../../../../services/event-filter-condition.service';

@Component({
  selector: 'app-condition-editor',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    DialogModule,
    ButtonModule,
    InputTextModule,
    InputNumberModule,
    SelectModule,
    CheckboxModule,
    SearchableTreeComponent,
    DatePickerModule,
  ],
  templateUrl: './condition-editor.component.html',
  styleUrls: ['./condition-editor.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ConditionEditorComponent implements OnInit {
  visible = model<boolean>(false);
  readonly condition = input<FilterCondition | null>(null);
  protected clientDataService = inject(ClientDataService);
  protected eventFilterConditionService = inject(EventFilterConditionService);
  private readonly cdr = inject(ChangeDetectorRef);

  saved = output<FilterCondition>();
  cancelled = output<void>();

  ngOnInit(): void {
    const currentCondition = this.condition();
    if (isDefined(currentCondition)) {
      // First, get condition parts to create the cache entry
      const parts =
        this.eventFilterConditionService.getConditionParts(currentCondition);
      for (const part of parts) {
        if (
          part.type === 'checklist' &&
          !Array.isArray(currentCondition.values[part.key])
        ) {
          currentCondition.values[part.key] = [];
        }
      }

      // Then initialize async options
      void this.eventFilterConditionService
        .initializeConditionParts(currentCondition)
        .then(() => {
          // Force Angular to detect the change in the dropdown options
          this.cdr.detectChanges();
        });
    }
  }

  constructor() {
    effect(async () => {
      const currentCondition = this.condition();
      if (isDefined(currentCondition)) {
        // First, get condition parts to create the cache entry
        const parts =
          this.eventFilterConditionService.getConditionParts(currentCondition);
        for (const part of parts) {
          if (
            part.type === 'checklist' &&
            !Array.isArray(currentCondition.values[part.key])
          ) {
            currentCondition.values[part.key] = [];
          }
        }

        // Then initialize async options
        await this.eventFilterConditionService.initializeConditionParts(
          currentCondition,
        );

        // Force Angular to detect the change in the dropdown options
        this.cdr.detectChanges();
      }
    });
  }

  getConditionTitle(condition: FilterCondition): string {
    switch (condition.type) {
      case 'speed':
        return 'Speed Condition';
      case 'time':
        return 'Time Condition';
      case 'zone':
        return 'Zone Condition';
      case 'distance':
        return 'Distance Condition';
      case 'duration':
        return 'Duration Condition';
      case 'days':
        return 'Days of Week Condition';
      default:
        return `${condition.type.charAt(0).toUpperCase() + condition.type.slice(1)} Condition`;
    }
  }

  getConditionParts(condition: FilterCondition | null): ConditionPart[] {
    return this.eventFilterConditionService.getConditionParts(condition);
  }

  isConditionValid(): boolean {
    const condition = this.condition();
    if (isNullOrUndefined(condition)) return false;

    const parts = this.getConditionParts(condition);
    return parts.every((part) => {
      if (isDefined(part.required) && part.required === false) return true;

      if (
        part.type === 'checklist' &&
        Array.isArray(condition.values[part.key])
      ) {
        return true;
      }

      return (
        isDefined(condition.values[part.key]) &&
        condition.values[part.key] !== '' &&
        condition.values[part.key] !== null
      );
    });
  }

  onSave(): void {
    const condition = this.condition();
    if (isDefined(condition) && this.isConditionValid()) {
      this.eventFilterConditionService.updateConditionText(condition);
      this.saved.emit(condition);
    }
  }

  onVisibilityChange(isVisible: boolean): void {
    if (!isVisible) {
      this.visible.set(false);
      this.onCancel();
    }
  }

  onCancel(): void {
    this.cancelled.emit();
  }

  async onSearchableTreeNodeSelected(
    node: Record<string, unknown>,
    fieldKey: string,
  ): Promise<void> {
    const condition = this.condition();
    if (isNullOrUndefined(condition)) return;

    switch (fieldKey) {
      case 'zone':
        condition.values['zoneId'] =
          (node['id'] as string | number) ??
          (node['targetId'] as string | number);
        condition.values['zoneName'] =
          (node['name'] as string) ?? (node['targetName'] as string);
        break;

      case 'asset':
        condition.values['assetId'] =
          (node['id'] as string | number) ??
          (node['actorId'] as string | number);
        condition.values['assetName'] =
          (node['name'] as string) ?? (node['actorName'] as string);
        break;

      case 'input':
        // Store the selected digital input details
        condition.values['inputName'] =
          (node['name'] as string) ?? (node['text'] as string);
        break;

      // Add more cases for other entity types as needed
    }
  }

  // Add method to handle field changes for linked dropdowns
  async onFieldChange(fieldKey: string): Promise<void> {
    const condition = this.condition();
    if (isDefined(condition)) {
      await this.eventFilterConditionService.onConditionFieldChange(
        condition,
        fieldKey,
      );
      // Force Angular to detect the change in the dropdown options
      this.cdr.detectChanges();
    }
  }
}
