import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class UtilsService {
  constructor() {}

  getLanguages(): { id: string; name: string }[] {
    return [
      { id: 'en-us', name: 'English' },
      { id: 'fr', name: 'Français' },
      { id: 'es', name: 'Español' },
      { id: 'pt', name: 'Português' },
      { id: 'nl', name: 'Nederlands' },
      { id: 'de', name: 'Deutsche' },
      { id: 'it', name: 'Italiano' },
    ];
  }
}
