import {
  Component,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  OnInit,
  inject,
  input,
  forwardRef,
  signal,
  effect,
  ViewChild,
} from '@angular/core';
import {
  FormsModule,
  ControlValueAccessor,
  ReactiveFormsModule,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { CommonModule } from '@angular/common';

import { TreeNode } from 'primeng/api';
import { ButtonModule } from 'primeng/button';
import { InputTextModule } from 'primeng/inputtext';

import { isDefined, isNullOrUndefined } from '../../../../utils';
import { EventFilterService } from '../../../../services/event-filter.service';

import { TargetSelectorComponent } from '../target-selector/target-selector.component';
import { ConditionListComponent } from '../condition-list/condition-list.component';
import { ConditionEditorComponent } from '../condition-editor/condition-editor.component';
import { ActorSelectorComponent } from '../actor-selector/actor-selector.component';
import { cloneDeep } from 'lodash';
import { SelectModule } from 'primeng/select';
import { TreeSelect } from 'primeng/treeselect';
import {
  FilterItem,
  FilterCondition,
  TreeSelectNode,
  ActorNodeData,
  EventNodeData,
  TargetNodeData,
} from '../../../../models';
import { EventFilterConditionService } from '../../../../services/event-filter-condition.service';

export function provideEventFilterFieldValueAccessor() {
  return {
    provide: NG_VALUE_ACCESSOR,
    // eslint-disable-next-line @angular-eslint/no-forward-ref
    useExisting: forwardRef(() => EventFilterFieldComponent),
    multi: true,
  };
}
@Component({
  selector: 'app-event-filter-field',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ButtonModule,
    InputTextModule,
    SelectModule,
    ActorSelectorComponent,
    TargetSelectorComponent,
    ConditionListComponent,
    ConditionEditorComponent,
  ],
  templateUrl: './event-filter-field.component.html',
  styleUrls: ['./event-filter-field.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [provideEventFilterFieldValueAccessor()],
})
export class EventFilterFieldComponent implements ControlValueAccessor, OnInit {
  private readonly cdr = inject(ChangeDetectorRef);

  readonly id = input<string>();
  readonly required = input(false);
  readonly groupTypes = input<
    ('costcentres' | 'groups' | 'types' | 'categories')[]
  >([]);
  readonly targetNodes = signal<TreeSelectNode[]>([]);

  filterService = input<EventFilterService>(inject(EventFilterService));

  // Configuration inputs with defaults matching current behavior
  readonly minConditions = input<number>(0);
  readonly requireActorBeforeAction = input<boolean>(false);
  readonly requireActionForConditions = input<boolean>(true);

  actorNodes = signal<TreeSelectNode<ActorNodeData>[]>([]);
  actionNodes = signal<TreeSelectNode<EventNodeData>[]>([]);
  protected availableConditions = signal<TreeNode[]>([]);
  protected readonly eventFilterConditionService = inject(
    EventFilterConditionService,
  );

  value = signal<FilterItem>({ conditions: [] });
  get actor(): TreeSelectNode<ActorNodeData> | undefined {
    return this.value().actor;
  }
  set actor(newValue: TreeSelectNode<ActorNodeData> | undefined) {
    this.value.update((val) => ({ ...val, actor: newValue }));
    this.onEventFilterChange(this.transformFilterItemForOutput(this.value()));
    this.updateAvailableConditions();
  }

  get action(): TreeSelectNode<EventNodeData> | undefined {
    return this.value().action;
  }
  set action(newValue: TreeSelectNode<EventNodeData> | undefined) {
    this.value.update((val) => ({ ...val, action: newValue }));
    this.onEventFilterChange(this.transformFilterItemForOutput(this.value()));
    this.updateAvailableConditions();
    this.updateTargetNodes();
  }

  get target(): TreeSelectNode<TargetNodeData> | undefined {
    return this.value().target;
  }
  set target(newValue: TreeSelectNode<TargetNodeData> | undefined) {
    this.value.update((val) => ({ ...val, target: newValue }));
    this.onEventFilterChange(this.transformFilterItemForOutput(this.value()));
  }

  // Editor state
  selectedCondition: FilterCondition | null = null;
  selectedConditionIndex = -1;
  showConditionEditor = false;

  // Access dropdown components
  @ViewChild('actorDropdown') actorDropdown?: TreeSelect;
  @ViewChild('actionDropdown') actionDropdown?: TreeSelect;
  @ViewChild('targetDropdown') targetDropdown?: TreeSelect;
  @ViewChild('conditionDropdown') conditionDropdown?: TreeSelect;
  @ViewChild(ConditionEditorComponent)
  conditionEditor?: ConditionEditorComponent;
  touched = false;

  onEventFilterChange: (
    value: FilterItem | Record<string, unknown> | null,
  ) => void = () => {};
  onTouch: () => void = () => {};

  constructor() {
    effect(() => {
      const _actor = this.value().actor;
      const _action = this.value().action;

      if (isDefined(_action)) {
        this.updateAvailableConditions();
      }
    });
  }

  ngOnInit() {
    this.updateAvailableConditions();
    this.loadAllNodes().catch(() => {});
  }

  private transformFilterItemForOutput(
    value: FilterItem,
  ): Record<string, unknown> | null {
    if (isNullOrUndefined(value)) return null;

    return {
      actor: value.actor,
      action: value.action,
      target: value.target,
      conditions: value.conditions?.map((condition) => ({
        ...condition,
        // Transform any nested tree objects if needed
      })),
    };
  }

  async loadAllNodes(): Promise<void> {
    try {
      const [assetTree, actionTree] = await Promise.all([
        this.filterService().getAssetSelectionTree(this.groupTypes()),
        Promise.resolve(this.filterService().getActionEventsTree()),
      ]);

      this.actorNodes.set(assetTree);
      this.actionNodes.set(actionTree);
      this.cdr.markForCheck();
    } catch (error) {
      console.error('Error loading nodes:', error);
      this.actorNodes.set([]);
      this.actionNodes.set([]);
    }
  }

  private transformIncomingValue(
    value: FilterItem | Record<string, unknown>,
  ): FilterItem {
    const transformField = (
      fieldValue: unknown,
    ): TreeSelectNode | undefined => {
      if (!isDefined(fieldValue)) {
        return undefined;
      }
      // If fieldValue is plain data (not already a TreeNode-like object with a .data property),
      // wrap it. Otherwise, assume it's already a TreeNode or TreeNode-like.
      if (
        typeof fieldValue !== 'object' ||
        fieldValue === null ||
        (isNullOrUndefined((fieldValue as Record<string, unknown>)['data']) &&
          isNullOrUndefined((fieldValue as Record<string, unknown>)['key']))
      ) {
        // Added !fieldValue.key as another heuristic for plain data
        return { data: fieldValue } as TreeSelectNode; // This is a temporary wrapper
      }
      return fieldValue as TreeSelectNode;
    };

    return {
      actor: transformField(value.actor) as TreeSelectNode<ActorNodeData>,
      action: transformField(value.action) as TreeSelectNode<EventNodeData>,
      target: transformField(value.target) as TreeSelectNode<TargetNodeData>,
      conditions:
        (value.conditions as FilterCondition[]) ?? ([] as FilterCondition[]),
    };
  }

  // Helper to find a node by its key in a tree structure (recursive)
  private findNodeById(
    nodes: TreeSelectNode[],
    key: string | undefined,
  ): TreeSelectNode | undefined {
    if (
      isNullOrUndefined(key) ||
      isNullOrUndefined(nodes) ||
      nodes.length === 0
    )
      return undefined;
    for (const node of nodes) {
      if (node.id === key) return node;
      if (isDefined(node.children) && node.children.length > 0) {
        const foundInChildren = this.findNodeById(node.children, key);
        if (foundInChildren) return foundInChildren;
      }
    }
    return undefined;
  }

  // Helper to generate the key from actor data
  private generateActorKeyFromData(
    actorData: ActorNodeData,
  ): string | undefined {
    if (isNullOrUndefined(actorData)) return undefined;
    const actorSelectionType = actorData.actorSelectionType ?? '';
    const actorTypeId = actorData.actorTypeId;
    const actorId = actorData.actorId;

    if (actorSelectionType === 'any' && isDefined(actorTypeId)) {
      return `any.${actorTypeId}`;
    } else if (
      ['costcentre', 'group', 'category'].includes(actorSelectionType) &&
      isDefined(actorTypeId) &&
      isDefined(actorId)
    ) {
      return `${actorSelectionType}.${actorTypeId}.${actorId}`;
    }
    return actorData.actorId;
  }

  // Helper to generate the key from action data
  private generateActionKeyFromData(
    actionData: EventNodeData,
  ): string | undefined {
    if (isNullOrUndefined(actionData)) return undefined;
    const eventClass = actionData.eventClass;
    const eventType = actionData.eventType;

    if (isDefined(eventClass) && isDefined(eventType)) {
      return `event-${eventClass}-${eventType}`;
    } else if (isDefined(eventClass)) {
      return `event-${eventClass}`;
    }
    return undefined;
  }

  // Helper to generate key from target data (example, adjust if target key generation is different)
  private generateTargetKeyFromData(
    targetData: TargetNodeData,
  ): string | undefined {
    if (isNullOrUndefined(targetData)) return undefined;

    return targetData.targetId;
  }

  writeValue(value: FilterItem | Record<string, unknown>): void {
    // Allow 'any' for flexibility if external type isn't strictly FilterItem
    if (isDefined(value)) {
      const initialTransformedValue = this.transformIncomingValue(value);

      if (
        isDefined(initialTransformedValue.conditions) &&
        initialTransformedValue.conditions.length > 0
      ) {
        for (const condition of initialTransformedValue.conditions) {
          this.eventFilterConditionService
            .initializeConditionParts(condition)
            .catch(() => {});
        }
      }

      this.loadAllNodes()
        .then(async () => {
          let finalActorNode: TreeSelectNode | undefined = undefined;
          if (isDefined(initialTransformedValue.actor?.data)) {
            const actorKey = this.generateActorKeyFromData(
              initialTransformedValue?.actor?.data,
            );

            finalActorNode = this.findNodeById(this.actorNodes(), actorKey);
          }

          let finalActionNode: TreeNode | undefined = undefined;
          if (isDefined(initialTransformedValue?.action?.data)) {
            const actionKey = this.generateActionKeyFromData(
              initialTransformedValue?.action?.data,
            );
            finalActionNode = this.findNodeById(this.actionNodes(), actionKey);
          }

          let finalTargetNode: TreeNode | undefined = undefined;
          if (
            isDefined(initialTransformedValue.target?.data) &&
            isDefined(initialTransformedValue.action?.data?.targetType)
          ) {
            await this.filterService()
              .getTargetNodes(initialTransformedValue.action.data.targetType)
              .then((nodes) => {
                this.targetNodes.set(nodes);
              })
              .catch(() => {});

            const targetKey = this.generateTargetKeyFromData(
              initialTransformedValue?.target?.data,
            );
            finalTargetNode = this.findNodeById(this.targetNodes(), targetKey);
          }

          this.value.set({
            actor: finalActorNode as TreeSelectNode<ActorNodeData>,
            action: finalActionNode as TreeSelectNode<EventNodeData>,
            target: finalTargetNode as TreeSelectNode<TargetNodeData>,
            conditions: initialTransformedValue.conditions ?? [],
          });

          if (isDefined(this.value().action)) {
            this.updateAvailableConditions();
          }
          this.cdr.markForCheck();
        })
        .catch((error) => {
          console.error(
            'Error loading nodes or processing value in writeValue:',
            error,
          );
          // Fallback: set the initially transformed (but potentially unmatchable) value
          this.value.set(initialTransformedValue);
          this.cdr.markForCheck();
        });
    } else {
      this.value.set({
        actor: undefined,
        action: undefined,
        target: undefined,
        conditions: [],
      });
      this.cdr.markForCheck();
    }
  }

  registerOnChange(fn: (value: Record<string, unknown>) => void): void {
    // Wrap the provided callback with our transformer
    this.onEventFilterChange = (
      value: FilterItem | Record<string, unknown> | null,
    ) => {
      const transformedValue = this.transformFilterItemForOutput(
        value as FilterItem,
      );
      fn(transformedValue as Record<string, unknown>);
    };
  }

  registerOnTouched(fn: () => void): void {
    this.onTouch = fn;
  }

  setDisabledState(_isDisabled: boolean): void {
    // Handle disabled state if needed
  }

  /**
   * Constructs the proper actor ID based on the filter value
   */
  getActorNodeId(): string | undefined {
    const actor = this.value()?.actor;
    if (isNullOrUndefined(actor)) return undefined;

    const actorSelectionType = actor?.data?.actorSelectionType ?? '';
    const actorTypeId = actor?.data?.actorTypeId;
    const actorId = actor?.data?.actorId;

    if (actorSelectionType === 'any' && isDefined(actorTypeId)) {
      return `any.${actorTypeId}`;
    } else if (
      ['costcentre', 'group', 'category'].includes(actorSelectionType) &&
      isDefined(actorTypeId) &&
      isDefined(actorId)
    ) {
      return `${actorSelectionType}.${actorTypeId}.${actorId}`;
    }

    return actor?.data?.actorId;
  }

  /**
   * Constructs the proper action ID based on the filter value
   */
  getActionNodeId(): string | undefined {
    const actionNode = this.value()?.action; // actionNode is the full TreeNode
    if (isNullOrUndefined(actionNode) || isNullOrUndefined(actionNode.data))
      return undefined;
    // Use the generateActionKeyFromData helper if the logic is identical
    return actionNode.key ?? this.generateActionKeyFromData(actionNode.data);
  }

  canShowConditions(): boolean {
    if (this.requireActorBeforeAction() && isNullOrUndefined(this.actor)) {
      return false;
    }

    if (this.requireActionForConditions() && isNullOrUndefined(this.action)) {
      return false;
    }

    return true;
  }

  updateAvailableConditions(): void {
    if (isDefined(this.action?.data)) {
      this.availableConditions.set(
        this.filterService().getConditionsTree(this.action.data),
      );
      this.cdr.markForCheck();
    } else {
      this.availableConditions.set([]);
      this.cdr.markForCheck();
    }
  }

  updateTargetNodes(): void {
    if (isDefined(this.action?.data?.targetType)) {
      this.filterService()
        .getTargetNodes(this.action?.data?.targetType)
        .then((nodes) => {
          this.targetNodes.set(nodes);
          this.cdr.markForCheck();
        })
        .catch(() => {});
    }
  }

  // Handle condition add from condition list
  onAddCondition(node: TreeNode): void {
    if (isNullOrUndefined(node?.data)) return;

    const newCondition: FilterCondition = {
      id: node.data.id ?? Date.now().toString(),
      type: node.data.type,
      text: { display: '' },
      values: {},
    };

    this.selectedCondition = cloneDeep(newCondition);
    this.selectedConditionIndex = -1;
    this.showConditionEditor = true;

    this.touched = true;
    this.cdr.markForCheck();
  }

  onEditCondition(data: { condition: FilterCondition; index: number }): void {
    this.selectedCondition = cloneDeep(data.condition);
    this.selectedConditionIndex = data.index;
    this.showConditionEditor = true;
    this.cdr.markForCheck();
  }

  onSaveCondition(condition: FilterCondition): void {
    if (this.selectedConditionIndex >= 0) {
      const updatedConditions = [...this.value().conditions];
      updatedConditions[this.selectedConditionIndex] = condition;

      this.value.update((current) => ({
        ...current,
        conditions: updatedConditions,
      }));
    } else {
      this.value.update((current) => ({
        ...current,
        conditions: [...current.conditions, condition],
      }));
    }

    this.touched = true;
    this.onEventFilterChange(this.value());
    this.closeConditionEditor();
  }

  onRemoveCondition(condition: FilterCondition): void {
    const updatedConditions = this.value().conditions.filter(
      (c) => c !== condition,
    );

    this.value.update((current) => ({
      ...current,
      conditions: updatedConditions,
    }));

    this.touched = true;
    this.onEventFilterChange(this.value());
    this.cdr.markForCheck();
  }

  closeConditionEditor(): void {
    this.selectedCondition = null;
    this.selectedConditionIndex = -1;
    this.showConditionEditor = false;
    this.cdr.markForCheck();
  }

  isValid(): boolean {
    const filter = this.value();

    if (this.requireActorBeforeAction() && isNullOrUndefined(filter.actor)) {
      return false;
    }

    if (isNullOrUndefined(filter.action)) {
      return false;
    }

    if (
      isDefined(filter.action?.data?.targetType) &&
      isNullOrUndefined(filter.target)
    ) {
      return false;
    }

    if (filter.conditions.length < this.minConditions()) {
      return false;
    }

    const allConditionsValid = filter.conditions.every((condition) => {
      const hasRequiredValues = Object.keys(condition.values).length > 0;
      return hasRequiredValues && isDefined(condition.type);
    });

    return allConditionsValid;
  }
}
