import { inject, Injectable } from '@angular/core';
import {
  FormArray,
  FormBuilder,
  FormControl,
  FormGroup,
  ValidatorFn,
} from '@angular/forms';
import { BehaviorSubject } from 'rxjs';
import {
  FormFieldConfig,
  FormState,
  FormValue,
  ValidationRule,
} from '../models/form-field.model';
import { LocalStorageService } from './local-storage.service';
import { hasProperty, isDefined, isString } from '../utils';

@Injectable({
  providedIn: 'root',
})
export class DynamicFormService {
  private readonly formStateSubject = new BehaviorSubject<FormState | null>(
    null,
  );
  formState$ = this.formStateSubject.asObservable();
  private readonly STORAGE_KEY = 'dynamicFormState';
  private readonly fb = inject(FormBuilder);
  private readonly localStorageService = inject(LocalStorageService);

  createFormGroup(fields: FormFieldConfig[]): FormGroup {
    const group: Record<string, FormControl | FormArray | FormGroup> = {};

    fields.forEach((field) => {
      if (field.type === 'array') {
        group[field.name] = this.fb.array([]);
      } else if (field.type === 'group') {
        group[field.name] = this.createFormGroup(field.fields ?? []);
      } else {
        const initialValue = this.getInitialValue(field);
        group[field.key] = this.fb.control(
          initialValue,
          this.bindValidations(field.validations || []),
        );
      }
    });

    return this.fb.group(group);
  }

  private bindValidations(validations: ValidationRule[]): ValidatorFn[] {
    if (validations.length > 0) {
      return validations.map((valid) => valid.validator);
    }
    return [];
  }

  private getInitialValue(field: FormFieldConfig): unknown {
    if (isDefined(field.persistKey) && isString(field.persistKey)) {
      const savedState = this.localStorageService.getItem<string>(
        this.STORAGE_KEY,
      );

      if (isDefined(savedState) && isString(savedState)) {
        try {
          const state = JSON.parse(savedState) as FormState;
          if (hasProperty(state, field.persistKey)) {
            return state[field.persistKey];
          }
        } catch (error) {
          console.warn('Failed to parse saved state:', error);
        }
      }
    }
    return field.value;
  }

  saveState(formValue: FormValue): void {
    this.localStorageService.setItem<string>(
      this.STORAGE_KEY,
      JSON.stringify(formValue),
    );
  }

  updateFormState(state: FormState): void {
    this.formStateSubject.next(state);
    this.saveState(state);
  }

  clearSavedState(): void {
    this.localStorageService.removeItem(this.STORAGE_KEY);
    this.formStateSubject.next(null);
  }
}
