import {
  Component,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  OnInit,
  forwardRef,
  inject,
  input,
  HostListener,
  ElementRef,
  effect,
} from '@angular/core';
import {
  NG_VALUE_ACCESSOR,
  ControlValueAccessor,
  FormsModule,
} from '@angular/forms';
import { CommonModule } from '@angular/common';
import { AutoCompleteModule } from 'primeng/autocomplete';
import { ButtonModule } from 'primeng/button';
import { OverlayModule } from 'primeng/overlay';
import { TreeModule } from 'primeng/tree';
import { TabsModule } from 'primeng/tabs';
import { InputTextModule } from 'primeng/inputtext';
import { EntitiesService, AssetListItem } from '../../../../api';
import { isDefined, isNullOrUndefined } from '../../../utils';
import {
  ActorNodeData,
  TreeSelectNode,
} from '../../../models/tree-select.model';
import { firstValueFrom } from 'rxjs';
import {
  AssetFilterService,
  TagItem,
} from '../../../services/asset-filter.service';

interface InternalFilterValueSingle {
  mode: 'single';
  value: string | null; // asset id
}
interface InternalFilterValueMulti {
  mode: 'multi';
  value: ActorNodeData[]; // array of filter objects
}
type InternalFilterValue = InternalFilterValueSingle | InternalFilterValueMulti;

export function provideAssetFilterFieldValueAccessor() {
  return {
    provide: NG_VALUE_ACCESSOR,
    // eslint-disable-next-line @angular-eslint/no-forward-ref
    useExisting: forwardRef(() => AssetFilterFieldComponent),
    multi: true,
  };
}

@Component({
  selector: 'app-asset-filter-field',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    AutoCompleteModule,
    ButtonModule,
    OverlayModule,
    TreeModule,
    TabsModule,
    InputTextModule,
  ],
  templateUrl: './asset-filter-field.component.html',
  styleUrls: ['./asset-filter-field.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [provideAssetFilterFieldValueAccessor()],
})
export class AssetFilterFieldComponent implements ControlValueAccessor, OnInit {
  // Configurable Inputs
  multi = input(true); // if false -> single select
  required = input(false);
  readonly = input(false); // Add readonly input
  allowedAssetTypes = input<string[]>([]);
  excludeCategories = input(false);
  placeholder = input('Enter asset or vehicle name...');
  // Provide ability to pass initial ownerId (optional override)
  ownerId = input<string | undefined>(undefined);

  // State
  touched = false;
  dirty = false;
  loading = false;
  showPopup = false;
  truncatedList: { limit: number; count: number } | null = null;

  // Tag data
  selectedItems: TagItem[] = [];
  autoCompleteItems: TagItem[] = [];
  assetTreeNodes: TreeSelectNode<ActorNodeData>[] = [];
  groupTreeNodes: TreeSelectNode<ActorNodeData>[] = [];
  searchTerm = '';
  searchInput = '';

  // internal resolved owner id (either from input or client selection)
  private resolvedOwnerId: string | undefined;

  private onChange: (value: string | ActorNodeData[] | null) => void = () => {};
  private onTouched: () => void = () => {};
  private internalValue: InternalFilterValue = { mode: 'multi', value: [] };

  private readonly entities = inject(EntitiesService);
  private readonly assetFilterSvc = inject(AssetFilterService);
  private readonly cdr = inject(ChangeDetectorRef);
  private readonly host = inject(ElementRef<HTMLElement>);

  constructor() {
    effect(() => {
      const ownerIdValue = this.ownerId();
      this.resolvedOwnerId =
        ownerIdValue !== undefined &&
        ownerIdValue !== null &&
        ownerIdValue.trim() !== ''
          ? ownerIdValue
          : undefined;

      // Trigger fetches when ownerId becomes available
      if (
        this.resolvedOwnerId !== undefined &&
        this.resolvedOwnerId !== null &&
        this.resolvedOwnerId.trim() !== ''
      ) {
        void this.initForMode();
      }
    });
  }
  ngOnInit(): void {
    // Initial setup based on the current ownerId value
    const ownerIdValue = this.ownerId();
    this.resolvedOwnerId =
      ownerIdValue !== undefined &&
      ownerIdValue !== null &&
      ownerIdValue.trim() !== ''
        ? ownerIdValue
        : undefined;

    void this.initForMode();
  }

  // Control Value Accessor
  writeValue(extValue: unknown): void {
    if (!isDefined(extValue)) {
      this.internalValue = this.multi()
        ? { mode: 'multi', value: [] }
        : { mode: 'single', value: null };
      this.selectedItems = [];
      this.cdr.markForCheck();
      return;
    }

    if (this.multi()) {
      // expect extValue to be an array of filter objects
      if (Array.isArray(extValue)) {
        const typed = extValue as ActorNodeData[];
        this.internalValue = { mode: 'multi', value: typed };
        this.selectedItems = typed.map((x) => ({
          id: x.actorId,
          name: x.text ?? x.actorName ?? x.actorId,
          filter: x,
        }));
      }
    } else {
      // expect a single asset id
      const id =
        typeof extValue === 'string' && extValue.length ? extValue : null;
      this.internalValue = { mode: 'single', value: id };
      if (id !== null) {
        void this.fetchAsset(id).then((asset) => {
          if (asset) {
            this.selectedItems = [
              { id: asset.id, name: asset.name ?? asset.id, filter: null },
            ];
            this.cdr.markForCheck();
          }
        });
      } else {
        this.selectedItems = [];
      }
    }
    this.cdr.markForCheck();
  }

  registerOnChange(fn: (value: string | ActorNodeData[] | null) => void): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }
  setDisabledState(_isDisabled: boolean): void {
    // not implemented yet
  }

  // Initialization logic depending on mode
  private async initForMode(): Promise<void> {
    if (
      this.resolvedOwnerId === undefined ||
      this.resolvedOwnerId === null ||
      this.resolvedOwnerId === ''
    ) {
      return;
    }
    if (this.multi()) {
      this.groupTreeNodes = await this.assetFilterSvc.buildGroupTree({
        ownerId: this.resolvedOwnerId,
        allowedAssetTypes: this.allowedAssetTypes(),
        excludeCategories: this.excludeCategories(),
      });
    }
    await this.loadInitialAssets();
  }

  private async loadInitialAssets(): Promise<void> {
    this.loading = true;
    try {
      const res = await this.assetFilterSvc.getAssets(
        this.resolvedOwnerId ?? '',
        100,
      );
      if (res.truncated) {
        this.truncatedList = res.truncated;
      }
      const assets = res.items;
      this.assetTreeNodes = assets.map((a) => ({
        id: a.id,
        label: a.name ?? a.id,
        data: this.assetFilterSvc.toAssetFilter(a),
      }));
    } finally {
      this.loading = false;
      this.cdr.markForCheck();
    }
  }

  // Searching / autocomplete
  async onSearch(term: string): Promise<void> {
    term = (term || '').trim();
    if (!term) {
      this.autoCompleteItems = [];
      return;
    }
    this.loading = true;
    try {
      this.autoCompleteItems = await this.assetFilterSvc.search(term, {
        ownerId: this.resolvedOwnerId ?? '',
        multi: this.multi(),
        groupTreeNodes: this.groupTreeNodes,
        selectedIds: this.selectedItems.map((x) => x.id),
      });
    } catch {
      this.autoCompleteItems = [];
    } finally {
      this.loading = false;
      this.cdr.markForCheck();
    }
  }

  addTag(item: TagItem): void {
    if (!this.multi()) {
      this.selectedItems = [item];
    } else if (
      isNullOrUndefined(this.selectedItems.find((x) => x.id === item.id))
    ) {
      this.selectedItems = [...this.selectedItems, item];
    }
    this.updateExternalValue();
  }

  removeTag(item: TagItem): void {
    this.selectedItems = this.selectedItems.filter((x) => x.id !== item.id);
    this.updateExternalValue();
  }

  onTreeNodeSelect(event: unknown): void {
    let node: TreeSelectNode<ActorNodeData> | undefined;
    if (
      typeof event === 'object' &&
      event !== null &&
      'node' in (event as Record<string, unknown>)
    ) {
      node = (event as { node: TreeSelectNode<ActorNodeData> }).node;
    } else {
      node = event as TreeSelectNode<ActorNodeData>;
    }
    if (!node?.data) {
      return;
    }
    const data = node.data;
    const tag: TagItem = {
      id: node.id,
      name: data.text ?? node.label,
      filter: data,
    };
    this.addTag(tag);
    if (!this.multi()) {
      this.showPopup = false;
    }
  }

  clearSingle(): void {
    if (this.multi()) return;
    this.selectedItems = [];
    this.updateExternalValue();
  }

  private updateExternalValue(): void {
    this.dirty = true;
    if (this.multi()) {
      const filters: ActorNodeData[] = this.selectedItems.map(
        (x) => x.filter ?? this.assetFilterSvc.createSpecificFromTag(x),
      );
      this.internalValue = { mode: 'multi', value: filters };
      this.onChange(filters);
    } else {
      const id = this.selectedItems[0]?.id ?? null;
      this.internalValue = { mode: 'single', value: id };
      this.onChange(id);
    }
    this.cdr.markForCheck();
  }

  // Utilities
  private async fetchAsset(id: string): Promise<AssetListItem | null> {
    try {
      const asset = await firstValueFrom(this.entities.getAsset(id));
      return asset as unknown as AssetListItem;
    } catch {
      return null;
    }
  }

  // Autocomplete wrapper for PrimeNG event
  onAutoComplete(event: { query: string }): void {
    this.searchTerm = event.query;
    void this.onSearch(event.query);
  }

  onAutoItemSelected(event: { value: TagItem | null | undefined }): void {
    const item = event.value;
    if (item !== null && item !== undefined) {
      this.addTag(item);
    }
    // clear input after selection
    this.searchInput = '';
    this.autoCompleteItems = [];
  }

  // Validation state for host form integration
  get invalid(): boolean {
    return (
      this.required() &&
      this.touched &&
      (this.multi()
        ? this.selectedItems.length === 0
        : !this.selectedItems.length)
    );
  }

  // Check if the component should be disabled due to missing ownerId or explicit disabled input
  get isDisabled(): boolean {
    return (
      this.readonly() ||
      this.resolvedOwnerId === undefined ||
      this.resolvedOwnerId === null ||
      this.resolvedOwnerId.trim() === ''
    );
  }

  // Mark touched when focusing inside
  @HostListener('focusin') onFocusIn() {
    this.touched = true;
  }

  // Close popup when clicking outside component
  @HostListener('document:click', ['$event']) onDocClick(ev: MouseEvent) {
    if (!this.showPopup) return;
    const target = ev.target as HTMLElement | null;
    if (!(target instanceof HTMLElement)) return;
    // treat clicks inside host OR overlay popup (af-popup) as internal
    const insideHost = this.host.nativeElement.contains(target);
    const insideOverlay = !!target.closest('.af-popup');
    if (insideHost === false && insideOverlay === false) {
      this.showPopup = false;
      this.cdr.markForCheck();
    }
  }
}
