import { Component, output, input } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormControl, FormsModule, ReactiveFormsModule } from '@angular/forms';

import { ButtonModule } from 'primeng/button';
import { TreeSelectModule } from 'primeng/treeselect';
import { OverlayModule } from 'primeng/overlay';
import { TreeNode } from 'primeng/api';
import { TooltipModule } from 'primeng/tooltip';

import { isNullOrUndefined, isDefined } from '../../../../utils';
import { TreeNodeSelectEvent } from 'primeng/tree';
import { FilterCondition } from '../../../../models';

@Component({
  selector: 'app-condition-list',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    ButtonModule,
    TreeSelectModule,
    OverlayModule,
    TooltipModule,
  ],
  templateUrl: './condition-list.component.html',
  styleUrls: ['./condition-list.component.scss'],
})
export class ConditionListComponent {
  readonly conditions = input<FilterCondition[]>([]);
  readonly availableConditions = input<TreeNode[]>([]);
  readonly minConditions = input<number>(0);

  editCondition = output<{ condition: FilterCondition; index: number }>();
  removeCondition = output<FilterCondition>();
  addCondition = output<TreeNode>();

  showConditionDropdown = false;
  conditionFormControl = new FormControl(null);

  // Method to display human-readable condition text
  displayConditionText(condition: FilterCondition): string {
    if (isNullOrUndefined(condition)) return '';

    // First check if we have a text representation already
    if (isDefined(condition.text['display'])) {
      return condition.text['display'].trim();
    }

    // Fallback to constructing the text
    let displayText = '';
    switch (condition.type) {
      case 'speed': {
        const value = condition.values['value'] ?? '0';
        const unit = condition.values['unit'] ?? 'mph';
        const operator = condition.values['operator'] ?? 'gt';

        let operatorText = 'equal to';
        if (operator === 'gt') {
          operatorText = 'greater than';
        } else if (operator === 'lt') {
          operatorText = 'less than';
        }

        displayText = `speed is ${operatorText} ${value} ${unit}`;
        break;
      }
      case 'time': {
        const time = condition.values['time'] ?? '00:00';
        displayText = `time is ${time}`;
        break;
      }
      case 'zone': {
        const zoneName =
          condition.values['zoneName'] ??
          condition.text['zoneName'] ??
          'unknown zone';
        displayText = `inside zone ${zoneName}`;
        break;
      }
      case 'distance': {
        const value = condition.values['value'] ?? '0';
        const unit = condition.values['unit'] ?? 'mi';
        displayText = `distance is ${value} ${unit}`;
        break;
      }
      case 'duration': {
        const value = condition.values['value'] ?? '0';
        const unit = condition.values['unit'] ?? 'seconds';
        displayText = `duration is ${value} ${unit}`;
        break;
      }
      case 'days': {
        const days = (condition.values['days'] as string[]) ?? [];
        if (days.length > 0) {
          displayText = `on days: ${days.join(', ')}`;
        } else {
          displayText = 'on all days';
        }
        break;
      }
      case 'active': {
        displayText = 'condition is active';
        break;
      }
      default:
        displayText = `condition of type ${condition.type}`;
    }

    return displayText;
  }

  onConditionClicked(condition: FilterCondition, index: number): void {
    this.editCondition.emit({ condition, index });
  }

  onConditionRemoved(condition: FilterCondition): void {
    if (this.conditions().length > this.minConditions()) {
      this.removeCondition.emit(condition);
    }
  }

  onConditionSelected(event: TreeNodeSelectEvent): void {
    if (isDefined(event?.node)) {
      this.addCondition.emit(event.node);
      this.showConditionDropdown = false;
      this.conditionFormControl.reset();
    }
  }

  canRemoveCondition(): boolean {
    return this.conditions().length > this.minConditions();
  }
}
