import { inject, Injectable, OnDestroy } from '@angular/core';

import { ClientDataService } from '../../features/clients/services/client-data.service';
import { isDefined, isNullOrUndefined } from '../utils';
import { Contact, EntitiesService } from '../../api';
import { firstValueFrom } from 'rxjs';
import { AuthDataService } from '../../core/services/auth-data.service';
import { UtilityService } from '../../core/services/utility.service';
import { AssetGroupingItem, AssetGroupingTreeItem } from '../models';
import { RequestCacheService } from './request-cache.service';
import { DEFAULT_SYSTEM_ID } from '../../core/models';

interface EntityItem {
  id: string;
  name?: string | null;
  parent?: { id: string | null };
  index?: number | null;
  contacts?: Contact[] | null;
  [key: string]: unknown;
}

@Injectable({
  providedIn: 'root',
})
export class AssetGroupingService implements OnDestroy {
  private readonly clientDataService = inject(ClientDataService);
  private readonly entitiesService = inject(EntitiesService);
  private readonly authDataService = inject(AuthDataService);
  private readonly utilityService = inject(UtilityService);
  private readonly cacheService = inject(RequestCacheService);
  private cache: Record<
    string,
    { items: AssetGroupingItem[]; limit: number; count: number }
  > = {};

  private readonly globalClientId =
    this.clientDataService.getSelectedClient()?.id;

  private buildFilterQuery(filter?: string): string {
    return isDefined(filter)
      ? `&name=*${this.utilityService.escapeRqlValue(filter)}*`
      : '';
  }

  /**
   * Generic method to fetch and cache entities
   * @param entityType Type of entity to fetch ('costCentres', 'assetGroups', etc.)
   * @param clientId Client ID (owner ID)
   * @param fetchFn Function that returns a Promise with the API response
   * @param filter Optional filter string
   * @returns Promise of AssetGroupingItems
   */
  private async getEntities(
    entityType: string,
    clientId: string | undefined,
    fetchFn: (
      ownerId: string,
      filter?: string,
    ) => Promise<{
      items: EntityItem[];
      count: number;
      limit: number;
    }>,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    const ownerId = clientId ?? this.globalClientId ?? '';
    const cacheKey = `${ownerId}/${entityType}`;

    // Return from cache if available
    const cachedItems = this.cache[cacheKey];
    if (isDefined(cachedItems)) {
      return cachedItems.items.map((x) => ({ ...x }));
    }

    // Fetch from API if not in cache
    const result = await fetchFn(ownerId, filter);
    result.items.sort((a, b) => (a.name ?? '').localeCompare(b.name ?? ''));
    this.cache[cacheKey] = {
      items: AssetGroupingService.toAssetGroupingItems(result.items),
      count: result.count,
      limit: result.limit,
    };
    // Apply client-side filtering if needed
    if (
      isDefined(filter) &&
      entityType !== 'costCentres' &&
      entityType !== 'assetGroups' &&
      entityType !== 'zoneGroups' &&
      entityType !== 'assetCategories'
    ) {
      return this.cache[cacheKey].items.filter(
        (item) =>
          item.name.toLowerCase().includes(filter.toLowerCase()) === true,
      );
    }
    return this.cache[cacheKey].items.map((x_1) => ({ ...x_1 }));
  }

  /**
   * Generic method to get entities as a tree
   * @param clientId Client ID
   * @param getItemsFn Function to get flat items
   * @param filter Optional filter string
   * @returns Promise of tree structure
   */
  private async getEntitiesAsTree(
    clientId: string | undefined,
    getItemsFn: (
      clientId?: string,
      filter?: string,
    ) => Promise<AssetGroupingItem[]>,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    const result = await getItemsFn(clientId, filter);
    return AssetGroupingService.toTree(result);
  }

  /**
   * Generic tree traversal utility that can be used for different tree operations
   * @param tree The tree structure to traverse
   * @param visitor Visitor function for each node
   * @returns Whether traversal was stopped early
   */
  private traverseTree<T>(
    tree: AssetGroupingTreeItem[],
    visitor: (node: AssetGroupingTreeItem) => { collect?: T; stop?: boolean },
  ): T[] {
    const results: T[] = [];

    const recurse = (nodes: AssetGroupingTreeItem[]): boolean => {
      for (const node of nodes) {
        const visit = visitor(node);

        if (visit.collect !== undefined) {
          results.push(visit.collect);
        }

        if (isDefined(visit.stop)) {
          return true; // Stop traversal
        }

        if (isDefined(node.children) && node.children.length > 0) {
          const shouldStop = recurse(node.children);
          if (shouldStop) {
            return true;
          }
        }
      }

      return false;
    };

    recurse(tree);
    return results;
  }

  static toTree(items: AssetGroupingItem[]): AssetGroupingTreeItem[] {
    const itemMap = new Map();

    for (const item of items) {
      itemMap.set(item.id, {
        ...item,
        name: item.name,
        label: item.name,
        children: [],
      });
    }
    const rootItems: AssetGroupingTreeItem[] = [];

    for (const item of items) {
      const treeItem = itemMap.get(item.id);

      if (
        isDefined(item.parent) &&
        item.parent !== DEFAULT_SYSTEM_ID &&
        itemMap.has(item.parent)
      ) {
        const parentItem = itemMap.get(item.parent);
        if (isDefined(treeItem) && isDefined(parentItem?.children)) {
          parentItem.children.push(treeItem);
        }
      } else if (isDefined(treeItem)) {
        rootItems.push(treeItem);
      }
    }

    const sortChildren = (items: AssetGroupingTreeItem[]) => {
      items.sort((a, b) => a.name.localeCompare(b.name));
      for (const item of items) {
        if (isDefined(item.children) && item.children.length > 0) {
          sortChildren(item.children);
        }
      }
    };

    sortChildren(rootItems);

    return rootItems;
  }

  static toAssetGroupingItems(entities: EntityItem[]): AssetGroupingItem[] {
    return entities.map((item) => ({
      id: item.id,
      name: item.name ?? '',
      parent: isDefined(item.parent) ? item.parent.id : DEFAULT_SYSTEM_ID,
      index: item.index ?? 0,
      contacts: item.contacts ?? [],
      data: item,
    }));
  }

  static treeToComboValues(
    tree: AssetGroupingTreeItem[],
    startingDepth = 0,
  ): { key: string; value: string; indent: number }[] {
    const result: { key: string; value: string; indent: number }[] = [];
    const recurseTree = (items: AssetGroupingTreeItem[], depth: number) => {
      if (isDefined(items)) {
        items.sort((a, b) => a.name.localeCompare(b.name));
        for (const item of items) {
          result.push({ key: item.id, value: item.name, indent: depth });
          if (isDefined(item.children) && item.children.length > 0) {
            recurseTree(item.children, depth + 1);
          }
        }
      }
    };
    recurseTree(tree, startingDepth);
    return result;
  }

  static createRootNode(clientId: string): AssetGroupingItem {
    const data = {
      id: DEFAULT_SYSTEM_ID,
      name: 'Root',
      parent: { id: null },
      contacts: [],
      owner: { id: clientId },
    };
    return {
      id: data.id,
      name: data.name,
      parent: null,
      data: data,
    };
  }

  ngOnDestroy() {
    this.clear();
  }

  clear() {
    this.cache = {};
  }

  /**
   * Find a cost center in the cost center tree
   */
  findCCInCCTree(
    tree: AssetGroupingTreeItem[],
    costCentre: AssetGroupingItem | undefined,
  ): AssetGroupingTreeItem[] {
    if (isNullOrUndefined(costCentre)) {
      return [];
    }

    return this.traverseTree<AssetGroupingTreeItem>(tree, (node) => ({
      collect: node.id === costCentre.id ? node : undefined,
    }));
  }

  /**
   * Filter cost centers using tree structure
   */
  getFilteredCostCentres(
    costCentreTree: AssetGroupingTreeItem[],
    costCentres: AssetGroupingItem[],
  ): AssetGroupingItem[] {
    const results: AssetGroupingItem[] = [];

    this.traverseTree<void>(costCentreTree, (node) => {
      const costCentre = costCentres.find((x) => x.id === node.id);
      if (isDefined(costCentre)) {
        results.push(costCentre);
      }
      return {}; // Continue traversal
    });

    return results;
  }

  /**
   * Find a node in the tree by ID
   */
  findInTree(
    tree: AssetGroupingTreeItem[],
    id: string,
  ): AssetGroupingTreeItem | null {
    for (const node of tree) {
      // Check if current node matches
      if (node.id === id) {
        return node;
      }

      // Recursively search children
      if (node.children && node.children.length > 0) {
        const found = this.findInTree(node.children, id);
        if (found) {
          return found;
        }
      }
    }
    return null;
  }

  getGroupResultInfo(
    clientId: string,
    type:
      | 'costCentres'
      | 'assetGroups'
      | 'zoneGroups'
      | 'assetCategories'
      | 'assetTypes'
      | 'deviceTypes'
      | 'assetTagTypes',
  ): { limit: number; count: number } {
    const { count, limit } = this.cache[`${clientId}/${type}`];
    return { count, limit };
  }

  getCostCentres(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.getEntities(
      'costCentres',
      clientId,
      (ownerId, filter) =>
        firstValueFrom(
          this.entitiesService.listCostCentres(
            ownerId,
            0,
            1000,
            'name',
            this.buildFilterQuery(filter),
          ),
        ),
      filter,
    );
  }

  getCostCentresAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getCostCentres.bind(this),
      filter,
    );
  }

  async getCostCentresAsTreeForUser(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    const items = await this.getCostCentresAsTree(clientId, filter);
    const costCenterId = this.authDataService.getUserProfile()?.costCentre?.id;
    if (isDefined(costCenterId)) {
      // we need to filter out items that are not children of the user's cost centre. to do that, turn it into a tree and then back into a list again
      const item = this.findInTree(items, costCenterId);
      return [item].filter((x) => isDefined(x));
    }
    return items;
  }

  getAssetGroups(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.cacheService.getOrFetchAsync(
      `assetGroups-${clientId ?? this.globalClientId}-${filter ?? ''}`,
      () =>
        this.getEntities(
          'assetGroups',
          clientId,
          (ownerId, filter) =>
            firstValueFrom(
              this.entitiesService.listAssetGroups(
                ownerId,
                0,
                1000,
                'name',
                this.buildFilterQuery(filter),
              ),
            ),
          filter,
        ),
      2 * 60 * 1000, // 2 minutes - groups may change more often
    );
  }

  getAssetGroupsAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getAssetGroups.bind(this),
      filter,
    );
  }

  getAssetCategories(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.cacheService.getOrFetchAsync(
      `assetCategories-${clientId ?? this.globalClientId}-${filter ?? ''}`,
      () =>
        this.getEntities(
          'assetCategories',
          clientId,
          (ownerId, filter) =>
            firstValueFrom(
              this.entitiesService.listAssetCategories(
                ownerId,
                0,
                1000,
                'name',
                this.buildFilterQuery(filter),
              ),
            ),
          filter,
        ),
      5 * 60 * 1000, // 5 minutes - categories change infrequently
    );
  }

  getAssetCategoriesAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getAssetCategories.bind(this),
      filter,
    );
  }

  async getAssetTypes(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.getEntities(
      'assetTypes',
      clientId,
      async (ownerId) =>
        firstValueFrom(this.entitiesService.listAssetTypes(ownerId)),
      filter,
    );
  }

  async getAssetTypesAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getAssetTypes.bind(this),
      filter,
    );
  }

  async getDeviceTypes(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.getEntities(
      'deviceTypes',
      clientId,
      async (ownerId) =>
        firstValueFrom(this.entitiesService.listDeviceTypes(ownerId)),
      filter,
    );
  }

  getDeviceTypesAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getDeviceTypes.bind(this),
      filter,
    );
  }

  getAssetTagTypes(
    clientId?: string,
    _filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.getEntities('assetTagTypes', clientId, async (ownerId) =>
      firstValueFrom(this.entitiesService.listAssetTagTypes(ownerId)),
    );
  }

  getAssetTagTypesAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getAssetTagTypes.bind(this),
      filter,
    );
  }

  getZoneGroups(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingItem[]> {
    return this.getEntities(
      'zoneGroups',
      clientId,
      (ownerId, filter) =>
        firstValueFrom(
          this.entitiesService.listZoneGroups(
            ownerId,
            0,
            1000,
            'name',
            this.buildFilterQuery(filter),
          ),
        ),
      filter,
    );
  }

  getZoneGroupsAsTree(
    clientId?: string,
    filter?: string,
  ): Promise<AssetGroupingTreeItem[]> {
    return this.getEntitiesAsTree(
      clientId,
      this.getZoneGroups.bind(this),
      filter,
    );
  }
}
