import { Component, effect, input, model } from '@angular/core';
import { isDefined, isNullOrUndefined } from '../../../../utils';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { OverlayModule } from 'primeng/overlay';
import { TreeSelectModule } from 'primeng/treeselect';
import {
  ActorNodeData,
  EventNodeData,
  TreeSelectNode,
} from '../../../../models';

@Component({
  selector: 'app-actor-selector',
  imports: [CommonModule, FormsModule, TreeSelectModule, OverlayModule],
  templateUrl: './actor-selector.component.html',
  styleUrl: './actor-selector.component.scss',
})
export class ActorSelectorComponent {
  readonly actorNodes = input<TreeSelectNode<ActorNodeData>[]>([]);
  readonly actionNodes = input<TreeSelectNode<EventNodeData>[]>([]);
  readonly requireActorBeforeAction = input(true);
  readonly actorId = input<string | undefined>(undefined);
  readonly actionId = input<string | undefined>(undefined);

  actor = model<TreeSelectNode<ActorNodeData> | undefined>(undefined);
  action = model<TreeSelectNode<EventNodeData> | undefined>(undefined);

  showActorDropdown = false;
  showActionDropdown = false;

  constructor() {
    // Use effect to synchronize IDs with tree nodes
    effect(() => {
      const actorIdValue = this.actorId();
      const actionIdValue = this.actionId();

      if (isDefined(actorIdValue) && this.actorNodes()?.length > 0) {
        const matchedActor = this.findActorNodeById(
          this.actorNodes(),
          actorIdValue,
        );

        if (isDefined(matchedActor) && matchedActor !== this.actor()) {
          this.actor.set(matchedActor);
        }
      }

      if (isDefined(actionIdValue) && this.actionNodes()?.length) {
        const matchedAction = this.findActionNodeById(
          this.actionNodes(),
          actionIdValue,
        );
        if (isDefined(matchedAction) && matchedAction !== this.action()) {
          this.action.set(matchedAction);
        }
      }
    });
  }

  onActorSelected(): void {
    this.showActorDropdown = false;
  }

  onActionSelected(): void {
    this.showActionDropdown = false;
  }

  handleActionClick(): void {
    // Only require actor if there are actor nodes available and actor is required
    if (
      this.requireActorBeforeAction() &&
      this.actorNodes().length > 0 &&
      isNullOrUndefined(this.actor())
    ) {
      // Don't show action dropdown if actor is required but not selected
      return;
    }

    this.showActionDropdown = true;
  }

  private findActorNodeById(
    nodes: TreeSelectNode<ActorNodeData>[],
    id: string,
  ): TreeSelectNode<ActorNodeData> | undefined {
    if (isNullOrUndefined(nodes) || !id) return undefined;

    for (const node of nodes) {
      if (node.id === id || node.data?.actorId === id) {
        return node;
      }

      if (isDefined(node.children)) {
        const found = this.findActorNodeById(node.children, id);
        if (found) return found;
      }
    }

    return undefined;
  }

  private findActionNodeById(
    nodes: TreeSelectNode<EventNodeData>[],
    id: string,
  ): TreeSelectNode<EventNodeData> | undefined {
    if (isNullOrUndefined(nodes) || !id) return undefined;

    for (const node of nodes) {
      if (node.id === id || node.data?.eventType === id) {
        return node;
      }

      if (isDefined(node.children)) {
        const found = this.findActionNodeById(node.children, id);
        if (found) return found;
      }
    }

    return undefined;
  }
}
