import {
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
  ElementRef,
  OnDestroy,
  inject,
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  forwardRef,
} from '@angular/core';
import { CommonModule } from '@angular/common';
import {
  ControlValueAccessor,
  FormsModule,
  NG_VALUE_ACCESSOR,
} from '@angular/forms';
import { TreeModule, TreeNodeSelectEvent } from 'primeng/tree';
import { TreeNode } from 'primeng/api';
import { InputTextModule } from 'primeng/inputtext';
import { ButtonModule } from 'primeng/button';
import { OverlayModule } from 'primeng/overlay';
import {
  Subject,
  debounceTime,
  distinctUntilChanged,
  takeUntil,
  switchMap,
  of,
} from 'rxjs';
import { EntitiesService } from '../../../api';
import { isDefined, isNullOrUndefined } from '../../utils';
import { TreeSelectModule } from 'primeng/treeselect';

/**
 * Represents a search result
 */
export interface SearchableTreeResult<T = unknown> {
  id: string;
  value: string;
  data: T;
}

@Component({
  selector: 'app-searchable-tree',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    TreeModule,
    InputTextModule,
    ButtonModule,
    TreeSelectModule,
    OverlayModule,
  ],
  templateUrl: './searchable-tree.component.html',
  styleUrls: ['./searchable-tree.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      // eslint-disable-next-line @angular-eslint/no-forward-ref
      useExisting: forwardRef(() => SearchableTreeComponent),
      multi: true,
    },
  ],
})
export class SearchableTreeComponent
  implements OnInit, OnChanges, OnDestroy, ControlValueAccessor
{
  @Input() treeData: Record<string, unknown>[] = [];
  @Input() searchEnabled = true;
  @Input() searchPlaceholder = 'Search...';
  @Input() valueField = 'name';
  @Input() childrenField = 'children';
  @Input() idField = 'id';
  @Input() expandedByDefault = false;
  @Input() debounceTime = 300;
  @Input() clientId?: string;
  @Input() entityType?: string;
  @Input() displayField = 'label';
  @Input() disabled = false;

  @Output() nodeSelected = new EventEmitter<Record<string, unknown>>();
  @Output() searchChanged = new EventEmitter<string>();

  @ViewChild('searchInput') searchInput!: ElementRef;
  @ViewChild('treePanel') treePanel!: ElementRef;

  nodes: TreeNode[] = [];
  searchValue = '';
  displayValue = '';
  searchNodes: SearchableTreeResult[] = [];
  flattenedNodes: SearchableTreeResult[] = [];
  isSearching = false;
  showDropdown = false;

  // Control value accessor properties
  private onChange: (value: unknown) => void = () => {};
  private onTouched: () => void = () => {};
  private innerValue: Record<string, unknown> | null = null;

  private readonly searchSubject = new Subject<string>();
  private readonly destroy$ = new Subject<void>();
  private readonly entitiesService = inject(EntitiesService);
  private readonly cdr = inject(ChangeDetectorRef);

  constructor() {}

  ngOnInit(): void {
    this.transformData();
    this.setupSearchDebounce();

    // Set initial display value if a node is selected
    this.updateDisplayValueFromSelection();

    // Close dropdown when clicking outside
    document.addEventListener('click', this.onDocumentClick.bind(this));
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (isDefined(changes['treeData'])) {
      this.transformData();
    }

    if (isDefined(changes['selectedNodeKey'])) {
      this.updateDisplayValueFromSelection();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
    document.removeEventListener('click', this.onDocumentClick.bind(this));
  }

  // ControlValueAccessor implementation
  writeValue(value: Record<string, unknown>): void {
    if (value !== this.innerValue) {
      this.innerValue = value;

      if (isDefined(value)) {
        // If value is a complex object, extract the ID
        if (typeof value === 'object' && value !== null) {
          this.selectedNodeKey = (value[this.idField] as string) ?? null;
        } else {
          this.selectedNodeKey = value;
        }

        // Update display value
        this.updateDisplayValueFromSelection();
      } else {
        this.selectedNodeKey = null;
        this.displayValue = '';
      }

      this.cdr.markForCheck();
    }
  }

  registerOnChange(fn: (value: unknown) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
    this.cdr.markForCheck();
  }

  // Our component's public properties
  selectedNodeKey: string | null = null;

  onDocumentClick(event: MouseEvent): void {
    // Check if click is outside the component
    if (
      this.showDropdown &&
      isNullOrUndefined(
        this.treePanel?.nativeElement?.contains(event.target),
      ) &&
      isNullOrUndefined(this.searchInput?.nativeElement?.contains(event.target))
    ) {
      this.closeDropdown();
      this.cdr.markForCheck();
    }
  }

  updateDisplayValueFromSelection(): void {
    if (isDefined(this.selectedNodeKey)) {
      const selectedNode = this.findNodeById(this.selectedNodeKey);
      if (selectedNode) {
        this.displayValue = selectedNode.label ?? '';
        this.cdr.markForCheck();
      } else if (
        typeof this.innerValue === 'object' &&
        this.innerValue !== null
      ) {
        // For API-selected items that may not be in the tree
        this.displayValue =
          (this.innerValue[this.displayField] as string) ??
          (this.innerValue[this.valueField] as string) ??
          '';
      }
    }
  }

  findNodeById(id: string): TreeNode | null {
    const findNode = (nodes: TreeNode[]): TreeNode | null => {
      for (const node of nodes) {
        if (node.key === id) {
          return node;
        }
        if (node.children) {
          const foundNode = findNode(node.children);
          if (foundNode) return foundNode;
        }
      }
      return null;
    };

    return findNode(this.nodes);
  }

  setupSearchDebounce(): void {
    this.searchSubject
      .pipe(
        debounceTime(this.debounceTime),
        distinctUntilChanged(),
        takeUntil(this.destroy$),
        switchMap((searchTerm) => {
          if (!searchTerm || searchTerm.length < 2) {
            this.isSearching = false;
            this.searchNodes = [];
            this.cdr.markForCheck();
            return of([]);
          }

          this.isSearching = true;
          this.cdr.markForCheck();

          if (isDefined(this.entityType) && isDefined(this.clientId)) {
            return this.performApiSearch(searchTerm);
          } else {
            return of(this.performLocalSearch(searchTerm));
          }
        }),
      )
      .subscribe({
        next: (results) => {
          this.searchNodes = results;
          this.isSearching = false;
          this.searchChanged.emit(this.searchValue);
          this.cdr.markForCheck();
        },
        error: (error) => {
          console.error('Error during search:', error);
          this.isSearching = false;
          this.searchNodes = [];
          this.cdr.markForCheck();
        },
      });
  }

  onSearchInput(value: string): void {
    this.searchValue = value;
    this.searchSubject.next(value);
  }

  toggleDropdown(event: Event): void {
    if (this.disabled) return;

    event.stopPropagation();
    this.showDropdown = !this.showDropdown;

    if (this.showDropdown && isDefined(this.searchInput)) {
      setTimeout(() => {
        this.searchInput.nativeElement.focus();
      });
    }

    this.onTouched();
    this.cdr.markForCheck();
  }

  closeDropdown(): void {
    this.showDropdown = false;
    this.searchValue = '';
    this.searchNodes = [];
  }

  performLocalSearch(searchTerm: string): SearchableTreeResult[] {
    if (!searchTerm.trim()) {
      return [];
    }

    const term = searchTerm.toLowerCase();
    return this.flattenedNodes.filter((node) =>
      node.value.toLowerCase().includes(term),
    );
  }

  performApiSearch(searchTerm: string) {
    if (isNullOrUndefined(this.clientId)) {
      return of([]);
    }

    const filter = `name=*${searchTerm}*&state=active`;
    const limit = 10;

    switch (this.entityType) {
      case 'asset':
        return this.entitiesService
          .listAssets(this.clientId, 0, limit, 'name:asc', filter)
          .pipe(
            switchMap((response) => {
              if (isNullOrUndefined(response.items)) return of([]);

              return of(
                response.items.map((item) => ({
                  id: item.id,
                  value: item.name || 'Unnamed Asset',
                  data: {
                    ...item,
                    text: `${item.assetType?.name ?? 'Asset'} "${
                      item.name ?? 'Unnamed'
                    }"`,
                    actorId: item.id,
                    actorName: item.name,
                    actorSelectionType: 'specific',
                    actorType: 'asset',
                    actorTypeId: item.assetType?.id,
                    actorTypeName: item.assetType?.name ?? 'Asset',
                  },
                })),
              );
            }),
          );

      case 'zone':
        return this.entitiesService
          .listZones(this.clientId, 0, limit, 'name:asc', filter)
          .pipe(
            switchMap((response) => {
              if (isNullOrUndefined(response.items)) return of([]);

              return of(
                response.items.map((item) => ({
                  id: item.id,
                  value: item.name ?? 'Unnamed Zone',
                  data: {
                    ...item,
                    text: `${item.zoneType ?? 'Zone'} "${
                      item.name ?? 'Unnamed'
                    }"`,
                    targetId: item.id,
                    targetName: item.name,
                    targetSelectionType: 'specific',
                    targetType: 'zone',
                    targetTypeId: item.zoneType,
                    targetTypeName: item.zoneType ?? 'Zone',
                  },
                })),
              );
            }),
          );

      case 'device':
        return this.entitiesService
          .listDevices(this.clientId, 0, limit, 'name:asc', filter)
          .pipe(
            switchMap((response) => {
              if (isNullOrUndefined(response.items)) return of([]);

              return of(
                response.items.map((item) => ({
                  id: item.id,
                  value: item.name ?? 'Unnamed Device',
                  data: {
                    ...item,
                    text: `${item.deviceType?.name ?? 'Device'} "${
                      item.name ?? 'Unnamed'
                    }"`,
                    deviceId: item.id,
                    deviceName: item.name,
                  },
                })),
              );
            }),
          );

      default:
        return of(this.performLocalSearch(searchTerm));
    }
  }

  transformData(): void {
    if (!Array.isArray(this.treeData) || !this.treeData.length) {
      this.nodes = [];
      this.flattenedNodes = [];
      return;
    }

    this.nodes = this.mapDataToTreeNodes(this.treeData);
    this.flattenedNodes = this.flattenTreeData(this.treeData);
  }

  mapDataToTreeNodes(data: Record<string, unknown>[]): TreeNode[] {
    if (!Array.isArray(data)) return [];

    return data.map((item) => {
      const node: TreeNode = {
        key: item[this.idField] as string,
        label:
          (item[this.displayField] as string) ??
          (item[this.valueField] as string),
        data: item,
        expanded: this.expandedByDefault,
      };

      const children = item[this.childrenField];

      if (Array.isArray(children) && children.length > 0) {
        node.children = this.mapDataToTreeNodes(children);
      }

      return node;
    });
  }

  flattenTreeData(data: Record<string, unknown>[]): SearchableTreeResult[] {
    if (!Array.isArray(data)) return [];

    let result: SearchableTreeResult[] = [];

    data.forEach((item) => {
      if (isDefined(item)) {
        // Add this item
        result.push({
          id: item[this.idField] as string,
          value:
            (item[this.displayField] as string) ??
            (item[this.valueField] as string),
          data: item,
        });

        // Add its children recursively
        const children = item[this.childrenField];

        if (Array.isArray(children) && children.length > 0) {
          result = [...result, ...this.flattenTreeData(children)];
        }
      }
    });

    return result;
  }

  onNodeSelect(event: TreeNodeSelectEvent): void {
    if (isDefined(event?.node?.data)) {
      this.displayValue = event.node.label ?? '';
      this.selectedNodeKey = event.node.key as string;

      // Update model values
      this.innerValue = event.node.data;
      this.onChange(this.innerValue);
      this.onTouched();

      this.nodeSelected.emit(event.node.data);
      this.closeDropdown();
      this.cdr.markForCheck();
    }
  }

  onSearchResultSelect(result: SearchableTreeResult): void {
    if (isNullOrUndefined(result)) return;

    this.displayValue = result.value;
    this.selectedNodeKey = result.id;

    // Update model values
    this.innerValue = result.data as Record<string, unknown>;
    this.onChange(this.innerValue);
    this.onTouched();

    this.nodeSelected.emit(result.data as Record<string, unknown>);
    this.closeDropdown();
    this.cdr.markForCheck();
  }
}
