import { Injectable, inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { EntitiesService, AssetListItem, AssetResponse } from '../../api';
import { AssetGroupingService } from './asset-grouping.service';
import { EventFilterService } from './event-filter.service';
import { AssetGroupingItem } from '../models';
import { ActorNodeData, TreeSelectNode } from '../models/tree-select.model';
import { isDefined } from '../utils';

// Public tag item interface used by component
export interface TagItem {
  id: string;
  name: string;
  filter: ActorNodeData | null; // selection descriptor
}

export interface AssetFetchResult {
  items: AssetListItem[];
  truncated?: { limit: number; count: number };
}

@Injectable({ providedIn: 'root' })
export class AssetFilterService {
  private assetsCache: Record<string, Promise<AssetFetchResult>> = {};
  private readonly entities = inject(EntitiesService);
  private readonly grouping = inject(AssetGroupingService);
  private readonly filterService = inject(EventFilterService);

  clearCache(): void {
    this.assetsCache = {};
  }

  async fetchAsset(id: string): Promise<AssetResponse | null> {
    try {
      return await firstValueFrom(this.entities.getAsset(id));
    } catch {
      return null;
    }
  }

  async getAssets(
    ownerId: string,
    limit: number,
    filter = '',
  ): Promise<AssetFetchResult> {
    const safeOwner = ownerId || '';
    const key = `${safeOwner}:${filter}:${limit}`;
    if (!(key in this.assetsCache)) {
      this.assetsCache[key] = firstValueFrom(
        this.entities.listAssets(
          safeOwner,
          0,
          limit,
          'name:asc',
          `owner.id=${safeOwner}&state=active${filter ? '&name=*' + encodeURIComponent(filter) + '*' : ''}`,
        ),
      ).then((res: { count: number; items: AssetListItem[] }) => {
        const truncated =
          typeof res?.count === 'number' && res.count > limit
            ? { limit, count: res.count }
            : undefined;
        return { items: Array.isArray(res?.items) ? res.items : [], truncated };
      });
    }
    return this.assetsCache[key];
  }

  async buildGroupTree(params: {
    ownerId: string;
    allowedAssetTypes?: string[];
    excludeCategories?: boolean;
  }): Promise<TreeSelectNode<ActorNodeData>[]> {
    try {
      const {
        ownerId,
        allowedAssetTypes = [],
        excludeCategories = false,
      } = params;
      const [costCentres, assetTypes, assetGroups, assetCategories] =
        await Promise.all([
          this.grouping.getCostCentres(ownerId),
          this.grouping.getAssetTypes(ownerId),
          this.grouping.getAssetGroups(ownerId),
          excludeCategories
            ? Promise.resolve([] as AssetGroupingItem[])
            : this.grouping.getAssetCategories(ownerId),
        ]);

      const filteredAssetTypes = assetTypes.filter(
        (t) =>
          isDefined(t?.name) &&
          (!allowedAssetTypes?.length ||
            allowedAssetTypes.includes(t.name.toLowerCase())),
      );

      return this.filterService.buildAssetSelectionTree(
        costCentres,
        filteredAssetTypes,
        assetGroups,
        assetCategories,
        allowedAssetTypes,
      );
    } catch (e) {
      console.error('Failed to build group tree', e);
      return [];
    }
  }

  flattenGroupNodes(nodes: TreeSelectNode<ActorNodeData>[]): TagItem[] {
    const result: TagItem[] = [];
    const walk = (n: TreeSelectNode<ActorNodeData>[]) => {
      n.forEach((node) => {
        if (node.data) {
          result.push({
            id: node.id,
            name: node.data.text ?? node.label,
            filter: node.data,
          });
        }
        if (Array.isArray(node.children) && node.children.length) {
          walk(node.children);
        }
      });
    };
    walk(nodes);
    return result;
  }

  toAssetFilter(a: AssetListItem): ActorNodeData {
    const assetAny = a;
    const assetTypeObj = assetAny.assetType;
    const assetTypeName = assetTypeObj?.name ?? 'Asset';
    return {
      actorId: a.id,
      actorName: a.name ?? a.id,
      actorSelectionType: 'specific',
      actorType: 'asset',
      actorTypeId: assetTypeObj?.id ?? '',
      actorTypeName: assetTypeName,
      text: `${assetTypeName} "${a.name}"`,
    };
  }

  createSpecificFromTag(tag: TagItem): ActorNodeData {
    return (
      tag.filter || {
        actorId: tag.id,
        actorSelectionType: 'specific',
        actorType: 'asset',
        actorTypeId: '',
        actorTypeName: 'Asset',
        text: tag.name,
      }
    );
  }

  async search(
    term: string,
    params: {
      ownerId: string;
      multi: boolean;
      groupTreeNodes: TreeSelectNode<ActorNodeData>[];
      selectedIds: string[];
    },
  ): Promise<TagItem[]> {
    const clean = term.trim();
    if (!clean) return [];
    const { ownerId, multi, groupTreeNodes, selectedIds } = params;
    const [assetRes, groups] = await Promise.all([
      this.getAssets(ownerId, 10, clean),
      multi
        ? Promise.resolve(this.flattenGroupNodes(groupTreeNodes))
        : Promise.resolve([]),
    ]);
    const groupMatches = groups.filter((g) =>
      g.name.toLowerCase().includes(clean.toLowerCase()),
    );
    const assetMatches: TagItem[] = assetRes.items.map((a) => ({
      id: a.id,
      name: a.name ?? a.id,
      filter: this.toAssetFilter(a),
    }));
    return [...assetMatches, ...groupMatches].filter(
      (x) => !selectedIds.includes(x.id),
    );
  }
}
